<template>
    <div class="my-popups">
        <loader :is-visible="isLoading"></loader>
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header theme-primary partition-full d-flex">
                        <h4 class="modal-title">{{ $t('SelectDefaultView') }}</h4>
                        <button type="button" class="close" v-on:click="Close">
                            <em class="fa fa-times" aria-hidden="true"></em>
                        </button>
                    </div>
                    <div class="modal-body">
                        <dynamic-form lang="en" :buttons="buttons" :schema="FormSchema" v-on:OnSubmit="onSubmit">
                        </dynamic-form>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import DataServices from "@/services/DataService";
export default {
    data() {
        return {
            isLoading: false,
            AutoServiceDisabled: true,
            FormSchema: [
                {
                    layoutType: "single",
                    Data: [
                        {
                            astype: "SelectField",
                            label: this.$t('DefaultView'),
                            name: "DefaultView",
                            value: "",
                            validationRules: { "required": true, max: 100 },
                            config: {
                                options: [{ value: "List", name: "List View" },{ value: "Gantt", name: "Gantt Chart" }]
                            }
                        },
                    ]
                }
            ],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Save",
                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Close",
                    onClick: this.Close
                }
            ],
        }
    },
    methods: {
        Close: function () {
            this.$parent.OpenSetDefaultView = false;
        },
        onSubmit(formfields, val) {
            var vm = this;
            var jsonobj = {
                view: formfields.DefaultView
            };
            var params = JSON.stringify(jsonobj);
            DataServices.SaveGanttViewFilter(params).then((response) => {
                if (response.data.status == "Success") {
                    vm.ShowAlert(vm.$t("DeafultViewAppliedSuccessfully", [vm.$t("DeafultView")]), "success", true, vm.$t("Alert"));
                    vm.$parent.loadView(formfields.DefaultView);
                    vm.Close();
                }
            });
        }
    }
}
</script>