<template>
    <div id="waiting">
        <div id="waitdetails">
            <div class="sa-srt-dtl" id="waitlist">
                <strong class="apmnt-no text-white p-2"><i class="fa fa-th"
                        style="opacity: .6; cursor: all-scroll; padding-top: 1px; font-size: 12px; float: left;"></i>
                    {{ data.Name }}(${{ data.workorder_number }})</strong>
                <template v-if="data.color != ''">
                    <template v-if="isColoreDark">
                        <span v-if="data.external_ticket_number" class="sa-color font-12 p-2 badge text-capitalize"
                            :style="{ 'background-color': data.color, 'color': '#FFFFFF' }">
                            {{ RemoveNull(data.TicketNumber) }} <br /><br />
                            {{ RemoveNull(data.external_ticket_number) }}
                        </span>
                        <span v-else class="sa-color font-12 p-2 badge text-capitalize"
                            :style="{ 'background-color': data.color, 'color': '#FFFFFF' }">
                            {{ RemoveNull(data.TicketNumber) }}
                        </span>
                    </template>
                    <template v-else>
                        <span v-if="data.external_ticket_number" class="sa-color font-12 p-2 badge text-capitalize"
                            :style="{ 'background-color': data.color, 'color': '#000000' }">
                            {{ RemoveNull(data.TicketNumber) }} <br /><br />
                            {{ RemoveNull(data.external_ticket_number) }}
                        </span>
                        <span v-else class="sa-color font-12 p-2 badge text-capitalize"
                            :style="{ 'background-color': data.color, 'color': '#000000' }">
                            {{ RemoveNull(data.TicketNumber) }}
                        </span>
                    </template>
                </template>
                <template v-else>
                    <span class="sa-color p-2"> {{ data.TicketNumber }}</span>
                </template>
                <div class="sa-detail">
                    <table class="table table-striped HS " style="margin:0px">
                        <tr>
                            <td  class="px-2 pt-0 pb-0"><i
                                    class="fa fa-ticket"></i>Work Order Number: {{ data.workorder_number }}</td>
                        </tr>
                        <tr>
                            <td  class="px-2 pt-0 pb-0"><i class="fa fa-ticket"></i> SA Number:
                                {{ data.Name }}</td>
                        </tr>
                        <tr>
                            <td  class="px-2 pt-0 pb-0"><i class="fa fa-ticket"></i> Ticket Number:
                                {{ data.TicketNumber }}</td>
                        </tr>
                        <tr>
                            <td  class="px-2 pt-0 pb-0"><i class="fa fa-cog"></i> Work Type:
                                {{ data.WorkType }}</td>
                        </tr>
                        <tr>
                            <td  class="px-2 pt-0 pb-0"><i class="fa fa-file-o"></i> Subject:
                                {{ data.Subject }}</td>
                        </tr>
                        <tr>
                            <td  class="px-2 pt-0 pb-0"><i class="fa fa-address-card"></i>
                                Account Name:{{ data.AccountName }}</td>
                        </tr>
                        <tr>
                            <td  class="px-2 pt-0 pb-0"><i class="fa fa-user"></i> Client:
                                {{ data.ContactName }}</td>
                        </tr>
                        <tr>
                            <td  class="px-2 pt-0 pb-0"><i class="fa fa-mail-forward"></i> Email:
                                {{ data.Email }}</td>
                        </tr>
                        <tr>
                            <td  class="px-2 pt-0 pb-0"><strong><i class="fa fa-calendar"></i>
                                    Status: </strong> {{ data.SA_Status }}</td>
                        </tr>
                        <tr>
                            <td  class="px-2 pt-0 pb-0"><i class="fa fa-address-card"></i>
                                Address: {{ data.Address }}</td>
                        </tr>
                        <tr>
                            <td class="px-2 pt-0 pb-0"><i class="fa fa-clock-o"></i>
                                Preferred Time: {{ data.StartDateTime }} - {{ data.EndDateTime }}</td>
                        </tr>
                    </table>
                </div>
            </div>
        </div>
        <p class="location-address ticket-address">{{ data.Address }}</p>
        <span class="location-address">WorkType:<b> {{ data.WorkType }}</b></span>&nbsp; | &nbsp;
        <span class="location-address">Status:<b> {{ data.SA_Status }}</b></span>
    </div>
</template>
<script>
export default {
    data() {
        return {
            data: {
                result: '',
                isColoreDark: true
            },
        };
    },
    created: function () {      
        this.getUpperNote(this.data.TicketNumber, this.data.external_ticket_number, this.data.WorkType, this.data.color);
    },
    methods: {
        RemoveNull(data) {
            if (data == null || (typeof data) == 'undefined') {
                return '';
            }
            else {
                return data;
            }
        },
        getUpperNote: function (ticket_value, ExternalTicketNumber, worktype_value, color) {
            var vm = this;
            vm.isColoreDark = true;
            if (color != null) {
                var colorCheck = color.substring(1);
                var rgb = parseInt(colorCheck, 16);
                var r = (rgb >> 16) & 0xff;
                var g = (rgb >> 8) & 0xff;
                var b = (rgb >> 0) & 0xff;
                var luma = 0.2126 * r + 0.7152 * g + 0.0722 * b;
                if (luma >= 127) {
                    vm.isColoreDark = false;
                } else {
                    vm.isColoreDark = true;
                }
            }
        }
    }
}
</script>