<template>
    <div class="my-popups">
        <loader :is-visible="isLoading"></loader>
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header theme-primary partition-full  d-flex">
                        <h4 class="modal-title">{{ $t('Service Crew Member') }}</h4>
                        <button type="button" class="close" v-on:click="Close()">
                            <em class="fa fa-times" aria-hidden="true"></em>
                        </button>
                    </div>
                    <div class="modal-body">
                        <div class="row">
                            <div class="col-4">
                                <div class="form-group mb-0">
                                    <input v-model="IsSearch" class="form-control text" type="text"
                                        placeholder="Searchby Service Crew Member" />

                                </div>
                            </div>
                            <div class="col-8 pl-0">
                                <div class="search-btm-btn background-0 text-left form-check-inline">
                                    <a title="Search"  href="javascript:;" @click="SearchCrew"
                                        class="btn btn-success mr-1 px-4"><i class="fa fa-search"></i></a>
                                    <a @click="ResetSearchCrew" href="javascript:;" title="Clear"
                                        class="btn btn-danger clearsearchtext px-4"><i class="fa fa-refresh"></i></a>
                                </div>
                            </div>
                            
                                <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12">
                                    <div class="listing mt-2" id="divTicketRouteListing">
                                        <div class="table-responsive table-fix-header">
                                            <table class="table table-bordered dt-responsive nowrap mt-0">
                                                <thead class="thead-bg">
                                                    <tr>
                                                        <th><span href="javascript:;" 
                                                                id="ResourceName">Resource Name</span></th>
                                                        <th><span href="javascript:;" 
                                                                id="ResourceType">Type</span></th>
                                                        <th><span>Start Date</span></th>
                                                        <th><span>End Date</span></th>
                                                    </tr>
                                                </thead>
                                                <tbody>

                                                    <tr v-for="(item,index) in CreWListData" :key="index">
                                                        <td>
                                                            <span title="ResourceName">{{ item.ResourceName }}</span>
                                                        </td>
                                                        <td>
                                                            <span title="ResourceType">{{ item.ResourceType }}</span>
                                                        </td>
                                                        <td>
                                                            <span title="StartDate">{{ item.StartDate }}</span>
                                                        </td>
                                                        <td>
                                                            <span title="EndDate">{{ item.EndDate }}</span>
                                                        </td>
                                                    </tr>
                                                    <tr v-if="noRecord">
                                                        <td colspan="4" class="text-center text-danger no-record">
                                                            <span >{{ $t('NoRecordfound') }}</span>
                                                        </td>
                                                        
                                                    </tr>
                                                </tbody>
                                            </table>
                                        </div>
                                    </div>
                                </div>
                           
                            <div class="row flex-row-reverse col-12 px-0 pt-3">
                                <div class="col-6 text-right px-0" @click="Close">
                                    <a  class="btn btn-danger"><i
                                            class="fa fa-times text-white pr-2"></i>Close</a>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import DataService from '../../services/DataService';
export default {
    props: {
        IsCrewListId: {
            Type: Number,
            required: true
        }
    },
    data() {
        return {
            TotalRecords: 0,
            PageNumber: 1,
            CurrentPage: 1,
            PageSize: 10,
            isPaged:1,
            SortBy: '',
            SortExp: '',
            TotalPages: 0,
            IsSearch:'',
            CreWListData:[],
            noRecord:false
        }
    },
    created: function () {        
        this.FetchCrewList();
    },
    methods: {
        FetchCrewList(){
            this.CreWListData=[];            
            var url=`crewId=${this.IsCrewListId}&search=${this.IsSearch}&pageNo=${this.PageNumber}&pageSize=${this.PageSize}&sortBy=${this.SortBy}&sortExp=${this.SortExp}`;
            DataService.GetCrewMemberListing(url).then(response=>{                
                if(JSON.parse(response.data).length>0){ 
                    this.CreWListData=JSON.parse(response.data)
                    this.noRecord=false;
                }
                else{
                    this.noRecord=true;
                }
            });
        },
        SearchCrew(){            
            this.FetchCrewList();
        },
        ResetSearchCrew(){            
            this.IsSearch='';
            this.FetchCrewList();
        },
        Close() {
            this.$parent.IsCrewMemberList = false;
        }
    }
}
</script>