<template>
    <div class="my-popups">
        <loader :is-visible="isLoading"></loader>
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header theme-primary partition-full d-flex">
                        <h4 class="modal-title">{{ $t('ScheduledAppointment') }}</h4>
                        <button type="button" class="close" v-on:click="CloseScheduledAppointment">
                            <em class="fa fa-times" aria-hidden="true"></em>
                        </button>
                        <span class="user-guide">
                            <a class="ancuserguide text-white" data-toggle="collapse" href="#collapseReplyUG" role="button"
                                aria-expanded="false" aria-controls="collapseExample"> <em
                                    class="fa fa-files-o pr-2"></em>{{ $t('GuideForUser') }}</a>
                            <div id="collapseReplyUG" class="divancuserguide collapse overflow-auto">
                                <div id="scrollbarreplyuserguideMangeView" class="custom-scrollbar-js">
                                    <div class="con" v-html="$t('UgScheduledAppointment')"></div>
                                </div>
                            </div>
                        </span>
                    </div>
                    <div class="modal-body">
                        <dynamic-form lang="en" :buttons="buttons" :schema="FormSchema" v-on:OnSubmit="onSubmit">
                            <template v-slot:tgslot-NoAssignedServiceResources="{ data }">
                                <div class="form-group p-2 bg-light mb-0" style="min-height:33px;">
                                    <span v-if="SchduleAppointment.AssignedResources != ''">{{
                                        SchduleAppointment.AssignedResources }}</span>
                                    <span v-else class="text-danger">{{$t('Noassignedresourcefound')}}</span>
                                </div>
                            </template>
                            <template v-slot:tgslot-NoAssignedServiceCrews="{ data }">
                                <div class="form-group p-2 bg-light mb-0" style="min-height:33px;">
                                    <span v-if="SchduleAppointment.AssignedCrews != ''">{{ SchduleAppointment.AssignedCrews
                                    }}</span>
                                    <span v-else class="text-danger">{{ $t('Noassignedcrewfound') }}</span>
                                </div>
                            </template>
                            <template v-slot:tgslot-RelatedSAList="{ data }">
                                <div class="listing" v-if="isLocateOrNonLocate != 'NU'">
                                    <div class="table-responsive table-fix-header" style="max-height:400px">
                                        <table id="realtedServiceAppointmentlistingdata"
                                            class="table table-bordered dt-responsive nowrap mt-0" style="width:100%">
                                            <thead class="thead-bg">
                                                <tr>
                                                    <th style="width:25%" class="text-center">{{ $t('AppointmentNumber') }}
                                                    </th>
                                                    <th style="width:20%" class="text-center">{{ $t('ScheduledSA') }}</th>
                                                    <th style="width:20%" class="text-center">{{ $t('Status') }}</th>
                                                    <th style="width:35%" class="text-left">{{ $t('WorkType') }}</th>
                                                </tr>
                                            </thead>
                                            <tbody>
                                                <tr v-for="(item, index) in relatedSAList" :key="index">
                                                    <td style="width:25%" class="text-center"><a
                                                            :href="'/ServiceAppointment/ViewSADetails/' + item.Id"
                                                            target="_blank">{{ item.AppointmentNo }}</a></td>
                                                    <td style="width:20%" class="text-center">
                                                        <label class="switch" v-if="isLocateOrNonLocate != 'NU'">
                                                            <input type="checkbox" hidden :value="item.Id" :id="item.Id"
                                                                v-model="checkedRelatedSAList" @click="check($event)" />
                                                            <span class="slider round"></span>
                                                        </label>
                                                        <label class="switch" v-if="isLocateOrNonLocate == 'NU'">
                                                            <input type="checkbox" disabled hidden :value="item.Id"
                                                                :id="item.Id" v-model="checkedRelatedSAList"
                                                                @click="check($event)" />
                                                            <span class="slider round"></span>
                                                        </label>
                                                    </td>
                                                    <td style="width:20%" class="text-center">{{ item.Status }}</td>
                                                    <td style="width:35%" class="text">{{ item.WorkType }}</td>
                                                </tr>
                                                <tr v-if="relatedSAList.length==0">
                                                    <td colspan="4" class="text-center bg-light text-danger">{{
                                                        $t('NoRecordfound') }}</td>
                                                </tr>
                                            </tbody>
                                        </table>
                                    </div>
                                </div>
                            </template>
                        </dynamic-form>
                        <div class="row">
                            <div class="col-lg-6">
                                <div class="py-2">
                                    <small class="text-danger">{{ $t('MandatoryString') }}</small>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import DataService from '../../services/DataService';
export default {
    props: {
        SchduleAppointment: {
            type: Object,
            required: true
        },
        SA_GroupData: {
            type: Array,
            required: true
        },
        IsCurrentView: {
            type: String,
            required: true
        }
    },
    data() {
        return {
            isLocateOrNonLocate: '',
            checkedRelatedSAList: [],
            Headers: [{
                "COLUMN_NAME": "AppointmentNo",
                "DATA_TYPE": "string",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Appointment Number",
                "DISPLAY_ORDER": 1
            },
            {
                "COLUMN_NAME": "Id",
                "DATA_TYPE": "string",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Scheduled SA",
                "DISPLAY_ORDER": 2
            },
            {
                "COLUMN_NAME": "Status",
                "DATA_TYPE": "string",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Status",
                "DISPLAY_ORDER": 3
            },
            {
                "COLUMN_NAME": "WorkType",
                "DATA_TYPE": "string",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Work Type",
                "DISPLAY_ORDER": 4
            }
            ],
            widgets: [],
            HeaderText: '',
            LegendArray: [],
            listheaderbuttons: [],
            isLoading: false,
            SortExp: '',
            SortBy: '',
            TotalRecords: 0,
            FormSchema: [
                {
                    layoutType: "double",
                    group_name: 'General Information',
                    Data: [
                        {
                            astype: "TextField",
                            label: this.$t('ScheduledAppointment'),
                            name: "ScheduledAppointment",
                            value: "",
                            disabled: true,
                            validationRules: { "required": false },
                        },
                        {
                            astype: "TextField",
                            label: this.$t('Acount'),
                            name: "Acount",
                            value: "",
                            disabled: true,
                            validationRules: { "required": false },
                        },
                        {
                            astype: "TextField",
                            label: this.$t('Subject'),
                            name: "Subject",
                            value: "",
                            disabled: true,
                            validationRules: { "required": false },
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('Status'),
                            name: "Status",
                            value: "",
                            config: {
                                options: []
                            },
                            placeholder: this.$t('SelectStatus'),
                            validationRules: { "required": true, max: 100 },
                        },
                        {
                            astype: "TextField",
                            label: this.$t('WorkType'),
                            name: "WorkType",
                            value: "",
                            disabled: true,
                            validationRules: { "required": false },
                        },
                        {
                            astype: "TextField",
                            label: this.$t('WorkOrder'),
                            name: "WorkOrder",
                            value: "",
                            disabled: true,
                            validationRules: { "required": false },
                        },
                    ],
                },
                {
                    layoutType: "single",
                    Data: [
                        {
                            astype: "TextAreaField",
                            label: this.$t('description'),
                            name: "Description",
                            value: "",
                            validationRules: "",
                            config: {
                                rows: 4,
                                cols: 5
                            }
                        },
                    ]
                },
                {
                    layoutType: "double",
                    group_name: 'Scheduled Times',
                    Data: [
                        {
                            astype: "DateField",
                            label: this.$t('ArrivalWindowStartTime'),
                            name: "ArrivalWindowStartTime",
                            value: '',                           
                            config: {mode:'DateTime', format: "M/D/YYYY" ,onChange: this.GetStartData}
                        },
                        {
                            astype: "DateField",
                            label: this.$t('ArrivalWindowEndTime'),
                            name: "ArrivalWindowEndTime",
                            value: '',
                            config: {mode:'DateTime', format: "M/D/YYYY",minDate: "" }
                        },
                        {
                            astype: "DateField",
                            label: this.$t('ScheduledStartTime'),
                            name: "ScheduledStartTime",
                            
                            value: '',
                            config: { mode:'DateTime',format: "MM/DD/YYYY h:mm A" ,onChange: this.GetStartDataa}
                        },
                        {
                            astype: "DateField",
                            label: this.$t('ScheduledEndTime'),
                            name: "ScheduledEndTime",
                            mode:'DateTime',
                            value: '',
                            config: {mode:'DateTime', format: "MM/DD/YYYY h:mm A",minDate: "" }
                        },
                        {
                            astype: "MultiSelectField",
                            label: this.$t('ServiceResource'),
                            name: "ServiceResource",
                            value: "",
                            mode: "tag",
                            placeholder: "",
                            validationRules: {
                                "required": false
                            },
                            config: {
                                options: []
                            },
                        },
                        {
                            astype: "SlotField",
                            label: this.$t('AssignedServiceResources'),
                            name: "NoAssignedServiceResources",
                            value: "",
                            placeholder: "",
                            visibility: true,
                            validationRules: "",
                            settings: {
                                isInSlot: true
                            }
                        },
                        {
                            astype: "MultiSelectField",
                            label: this.$t('ServiceCrew'),
                            name: "ServiceCrew",
                            value: "",
                            mode: "tag",
                            placeholder: "",
                            validationRules: {
                                "required": false
                            },
                            config: {
                                options: []
                            },
                        },
                        {
                            astype: "SlotField",
                            label: this.$t('SelectedServiceCrews'),
                            name: "NoAssignedServiceCrews",
                            value: "",
                            placeholder: "",
                            visibility: true,
                            validationRules: "",
                            settings: {
                                isInSlot: true
                            }
                        }
                    ]
                },
                {
                    layoutType: "single",
                    group_name: 'Related SA List',
                    Data: [
                        {
                            astype: "SlotField",
                            label: '',
                            name: "RelatedSAList",
                        },
                    ]
                },
            ],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Save",
                } , 
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Close",
                    onClick: this.CloseScheduledAppointment
                }
            ],
            statusList: [],
            resourceList: [],
            crewList: [],
            relatedSAList: [],
            isAjax: false
        }
    },
    created: async function () {  
        debugger
        await this.FetchData();
        await this.getStatusList();
        await this.getResourceList();
        await this.getcrewList();
        await this.getResourceCrewData();
        await this.getRelatedSAList(this.SchduleAppointment.SA_Account_Id, this.SchduleAppointment.SA_Id);
        this.isLocateOrNonLocate = this.GetUserInfo.UserType
    },
    methods: {
        check() { },
        GetStartData: function (event, id) {
            debugger
            var vueObj = this;
            (vueObj.FormSchema)[2].Data[1].config.minDate = event;
            (vueObj.FormSchema)[2].Data[3].config.minDate = event;
        },
        GetStartDataa: function (event, id) {
            debugger
            var vueObj = this;           
            (vueObj.FormSchema)[2].Data[3].config.minDate = event;  
        },
        FetchData: async function () {
            var vm = this;
            vm.FormSchema[0].Data[0].value = vm.SchduleAppointment.SA_Number;
            vm.FormSchema[0].Data[1].value = vm.SchduleAppointment.SA_Account;
            vm.FormSchema[0].Data[2].value = vm.SchduleAppointment.SA_Subject;
            vm.FormSchema[0].Data[3].value = vm.SchduleAppointment.Status_Id;
            vm.FormSchema[0].Data[4].value = vm.SchduleAppointment.SA_WorkType;
            vm.FormSchema[0].Data[5].value = vm.SchduleAppointment.SA_WorkOrder;
            vm.FormSchema[1].Data[0].value = vm.SchduleAppointment.SA_Description;
            vm.FormSchema[2].Data[2].value = vm.$options.filters.formatDateTime(vm.SchduleAppointment.SA_Scheduled_StartTime);
            vm.FormSchema[2].Data[3].value = vm.$options.filters.formatDateTime(vm.SchduleAppointment.SA_Scheduled_EndTime);
        },
        CloseScheduledAppointment: function () {
            this.$parent.IsSchduleAppointment = false;            
        },
        getRelatedSAList: async function (ths, Value) {
            ths=ths==null?0:ths;
            var url = `accountId=${ths}&sa_id=${Value}`;
            await DataService.getRelatedSAList(url).then(response => {
                this.relatedSAList = response.data.result.data;
            })
        },
        getResourceCrewData: async function () {
            var vueObj = this;
            var selectedResources;
            var selectedCrew;
            var url = `SA_ID=${vueObj.SchduleAppointment.SA_Id}`;
            await DataService.getResourceCrewData(url).then(response => {
                if (response.data != null) {
                    if (response.data.status != "Failure") {
                        response.data.result.data.forEach(item => {
                            selectedResources = $.grep(response.data.result.data, function (element, index) {
                                return (element.ResourceType == 'RESOURCE');
                            });
                            selectedCrew = $.grep(response.data.result.data, function (element, index) {
                                return (element.ResourceType == 'CREW');
                            });
                        })
                        if (selectedResources) {
                            var ResourceSelect = [];
                            selectedResources.forEach(item => {
                                ResourceSelect.push({
                                    label: item.name,
                                    value: item.value
                                })
                            });
                            vueObj.FormSchema[2].Data[4].value = ResourceSelect;
                        }
                        if (selectedCrew) {
                            var CrewSelect = [];
                            selectedCrew.forEach(item => {
                                CrewSelect.push({
                                    label: item.name,
                                    value: item.value
                                })
                            });
                            vueObj.FormSchema[2].Data[6].value = CrewSelect;
                        }
                        if (this.SA_GroupData.length > 0) {
                            if (this.SA_GroupData[0].type == 'Resource') {
                                var ResourceSelect = [];
                                this.SA_GroupData.forEach(item => {
                                    ResourceSelect.push({
                                        label: item.name,
                                        value: item.value
                                    })
                                });
                                vueObj.FormSchema[2].Data[4].value = ResourceSelect;
                            }
                            else if (this.SA_GroupData[0].type == 'Crew') {
                                var CrewSelect = [];
                                this.SA_GroupData.forEach(item => {
                                    CrewSelect.push({
                                        label: item.name,
                                        value: item.value
                                    })
                                });
                                vueObj.FormSchema[2].Data[6].value = CrewSelect;
                            }
                        }
                    }

                }
            });
        },
        getStatusList: async function () {
            var url = `type=status`
            await DataService.getStatusList(url).then(response => {
                if (response.data != null) {
                    this.statusList = response.data.result;
                    this.statusList.forEach(item => {
                        let obj = {
                            name: item.text,
                            value: item.value
                        };
                        this.FormSchema[0].Data[3].config.options.push(obj);
                    });
                }
            });
        },
        getResourceList: async function () {
            var url = `moduleName=&type=FSM_SA_RESOURCENAME&refId=`;
            await DataService.GetDDLDataFSM(url).then(response => {
                this.resourceList = response.data.result.DATA;
                this.resourceList.forEach(item => {
                    let obj = {
                        name: item.name,
                        value: item.value
                    };
                    this.FormSchema[2].Data[4].config.options.push(obj);
                });
            })
        },
        getcrewList: async function () {
            var url = `moduleName=&type=FSM_CREWNAME&refId=`;
            await DataService.GetDDLDataFSM(url).then(response => {
                this.crewList = response.data.result.DATA;
                this.crewList.forEach(item => {
                    let obj = {
                        name: item.name,
                        value: item.value
                    };
                    this.FormSchema[2].Data[6].config.options.push(obj);
                });
            })
        },
        onSubmit(formfields, val) {  
            debugger                      
            var vm = this;
            var relatedList = this.checkedRelatedSAList.join(',');
            var serviceResourceId = '';
            var serviceCrewId = '';
            var AdateStart = vm.$options.filters.formatDateTime(formfields.ArrivalWindowStartTime)==='Invalid Date'?vm.$options.filters.formatDateTime(vm.SchduleAppointment.ArrivalWindowStartTime):vm.$options.filters.formatDateTime(formfields.ArrivalWindowStartTime);
            var AdateEnd = vm.$options.filters.formatDateTime(formfields.ArrivalWindowEndTime)==='Invalid Date'?vm.$options.filters.formatDateTime(vm.SchduleAppointment.ArrivalWindowEndTime):vm.$options.filters.formatDateTime(formfields.ArrivalWindowEndTime);
            var AStartDate = vm.$options.filters.formatDateTime(formfields.ArrivalWindowStartTime)==='Invalid Date'?vm.$options.filters.formatDateTime(vm.SchduleAppointment.ArrivalWindowStartTime):vm.$options.filters.formatDateTime(formfields.ArrivalWindowStartTime);
            var AEndDate = vm.$options.filters.formatDateTime(formfields.ArrivalWindowEndTime)==='Invalid Date'?vm.$options.filters.formatDateTime(vm.SchduleAppointment.ArrivalWindowEndTime):vm.$options.filters.formatDateTime(formfields.ArrivalWindowEndTime);            
            if (AStartDate == AEndDate) {
                if (AdateStart > AdateEnd) {
                    vm.ShowAlert(vm.$t("ArrivalWindowTimeRangeValidation"), "warning", true, vm.$t('Alert'));
                    formfields.ArrivalWindowEndTime = "";
                    return false;
                }
            }    
            var SdateStart = vm.$options.filters.formatDateTime(formfields.ScheduledStartTime)==='Invalid date'?vm.$options.filters.formatDateTime(vm.SchduleAppointment.SA_Scheduled_StartTime):vm.$options.filters.formatDateTime(formfields.ScheduledStartTime);
            var SdateEnd = vm.$options.filters.formatDateTime(formfields.ScheduledEndTimes)==='Invalid date'?vm.$options.filters.formatDateTime(vm.SchduleAppointment.SA_Scheduled_EndTime):vm.$options.filters.formatDateTime(formfields.ScheduledEndTime);
            var SStartDate = vm.$options.filters.formatDateTime(formfields.ScheduledStartTime)==='Invalid date'?vm.$options.filters.formatDateTime(vm.SchduleAppointment.SA_Scheduled_StartTime):vm.$options.filters.formatDateTime(formfields.ScheduledStartTime);
            var SEndDate = vm.$options.filters.formatDateTime(formfields.ScheduledEndTime)==='Invalid date'?vm.$options.filters.formatDateTime(vm.SchduleAppointment.SA_Scheduled_EndTime):vm.$options.filters.formatDateTime(formfields.ScheduledEndTime);          
            if (SStartDate == SEndDate) {
                if (SdateStart > SdateEnd) {
                    vm.ShowAlert(vm.$t("ScheduledTimeRangeValidation"), "warning", true, vm.$t('Alert'));
                    formfields.ScheduledStartTime = "";
                    return false;
                }
            }
            if (formfields.ServiceResource) {
                var selectedResourceId = [];
                formfields.ServiceResource.forEach(item => {
                    selectedResourceId.push(item.value);
                });
                serviceResourceId = selectedResourceId.join(",");
            }
            if (formfields.ServiceCrew) {
                var selectedserviceCrewId = [];
                formfields.ServiceResource.forEach(item => {
                    selectedserviceCrewId.push(item.value);
                });
                serviceCrewId = selectedserviceCrewId.join(",");
            }
            var fullJson = {
                SA_id: vm.SchduleAppointment.SA_Id,
                txtSA_Number: formfields.ScheduledAppointment,
                txtAccount: formfields.Acount,
                Subject: formfields.Subject,
                satusId: formfields.Status,
                Description: formfields.Description,
                arrivalWindowStartTime: AStartDate,
                arrivalWindowEndTime: AEndDate,
                scheduledStartTime: SStartDate,
                scheduledEndTime: SEndDate,
                serviceResourceId: serviceResourceId,
                serviceCrewId: serviceCrewId,
                relatedServiceAppointments: relatedList,
            }
            var objectPostString = JSON.stringify(fullJson);
            DataService.AddScheduledAppointment(objectPostString).then(response => {
                if (response.data == 1) {
                    if(vm.$route.name=='WorkOrderListing'){
                        vm.$router.push({name: 'WorkOrderListing' });
                    }
                    if (vm.SchduleAppointment.SA_Id > 0) {
                        vm.ShowAlert(vm.$t('UpdatedSuccess', [vm.$t('ServiceAppointment')]), "success", true, vm.$t("Alert")); 
                        vm.CloseScheduledAppointment(); 
                        vm.$parent.fetchRecords();                      
                    }
                    else {
                        vm.ShowAlert(vm.$t('AddedSuccess', [vm.$t('ServiceAppointment')]), "success", true, vm.$t("Alert"));   
                        vm.CloseScheduledAppointment();   
                        vm.$parent.fetchRecords();                  
                    }
                    if (vm.isAjax == true) {
                        vm.CloseScheduledAppointment();
                    }
                    else {
                        if (vm.IsCurrentView == 'Gantt') {
                            vm.$parent.refreshGhanttChart();
                            vm.CloseScheduledAppointment();
                        } else {
                            vm.$router.push({ path: '/ServiceAppointment/Index' });
                            vm.CloseScheduledAppointment();
                        }
                    }
                }
                else {
                    vm.ShowAlert(vm.$t('AddedError', [vm.$t('ServiceAppointment')]), "failure", true, vm.$t("Alert"));
                }
            });

        },
        openPopupForSetupTickectSource(ths){            
            this.$router.push({ name: "ViewSADetails", params: { id: ths } });
        } 
    }


}
</script>
<style scoped>
#ArrivalWindowStartTime, #ArrivalWindowEndTime{z-index: 999;}
#ScheduledStartTime, #ScheduledEndTime{z-index: 998;}
#ServiceResource{z-index: 997;}
#ServiceCrew{z-index: 996;}

</style>