<template>
    <div id="divGanttView">
        <loader :is-visible="isLoading"></loader>
        <div class="panel">
            <div class="panel-header border-bottom btn-iconres">
                <div class="row">
                    <div class="col-md-3 d-flex align-items-center">
                        <select v-model="CurrentView" id="ddlCurrentView"  name="CurrentView"
                            v-bind:class="{ 'form-control': true }" @change="ChangeViewResource(CurrentView)">
                            <option v-for="(item, itemIndex) in ddlCurrentView" :key="itemIndex" v-bind:value="item.value">
                                {{ item.name }}</option>
                        </select>
                        <button ref="ancRefresh" class="btn btn-primary form-btns mx-2" @click="RefreshFilter"><span><i
                                    class="fa fa-refresh p-0"></i></span></button>
                        <button @click="ApplyFilter" tooltip="Filter" class="btn btn-white form-btns mr-2 border"><span><i
                                    class="fa fa-filter"></i></span></button>
                        <button ref="ancLock" class="btn btn-success form-btns mr-2" @click="UnLockUnSchduleList"><span><i
                                    class="fa fa-lock p-0"></i></span></button>
                    </div>
                    <div class="col-md-9 p-0 d-flex justify-content-end">
                        <div class="lock-gantt-view p-2 mr-4" style="background-color: #f2f2f2;">
                            <div class="timezone">
                                <div id="ancTimeZoneEdit" @click="TimeZoneEdit" v-if="isTimeZoneEdit" class="d-flex"> <a>
                                        <span id="spnTimeZone" style="font-size: 14px;">Current TimeZone:{{
                                            isZoneDisplayNamee }}</span>
                                    </a>
                                    <div class="edit-time-pencil pl-2">
                                        <i class="fa fa-pencil text-success"></i>
                                    </div>
                                </div>
                                <div id="divEditTimezone" v-if="!isTimeZoneEdit" class="d-flex">                                  
                                    <select v-model="isZoneDisplayNameId" name="DisplayNameZone" @change="ddlTimeZone"
                                        v-bind:class="{ 'form-control': true }">
                                        <option v-for="(item, itemIndex) in isZoneDisplayName" :key="itemIndex"
                                            :value="item.VALUE">{{ item.TEXT }}</option>
                                    </select>
                                    <a id="ancTimeZone" @click="ddlTimeZone"> <i
                                            class="mt-2 ml-2 fa fa-times text-danger"></i></a>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div class="panel-content p-3 pb-2 pagination2 table-responsive no-padding switchcalendar border-0">
                <div class="control-section">                   
                    <div class="content-wrapper row">
                        <div class="schedule-container e-schedule schedule-drag-drop px-3">
                            <ejs-schedule ref='ScheduleObj' id='Schedule' width='100%' height='550px'
                                :selectedDate='selectedDate' :currentView='IsView' :group="group"
                                :resourceHeaderTemplate="'resourceHeaderTemplate'" :renderCell='onRenderCell'
                                :cellDoubleClick="onAppointmentClick" :cellClick="onAppointmentClick"
                                :eventSettings='eventSettings' :actionComplete='onActionComplete'
                                :eventTemplate="'eventTemplate'" :dateHeaderTemplate='dateHeaderTemplate'
                                @eventClick="onEventClick" @eventRendered="onEventRendered" :rowAutoHeight='rowAutoHeight'>
                                <template v-slot:resourceHeaderTemplate="{ data }">
                                    <div className="template-wrap">
                                        <div class="specialist-category">
                                            <span class="specialist-image" v-html="getConsultantImageName(data)"></span>
                                            <div class="specialist-name" @click="GetConsultantName(data)"
                                                v-html="getConsultantName(data)"></div>                                           
                                            <span v-html="getConsultantDesignation(data)"></span>
                                        </div>
                                    </div>
                                </template>
                                <e-views>
                                    <e-view option='TimelineDay'></e-view>
                                    <e-view option='TimelineWeek'></e-view>
                                    <e-view option='TimelineMonth'></e-view>
                                </e-views> 
                                <e-resources>
                                    <!-- <template v-for="(gresource, indexX) in GroupData1">                                        
                                    <e-resource :key="indexX" :field='gresource.field' :title='gresource.title' :name='gresource.name' :dataSource='gresource.dataSource'
                                         :textField='gresource.textField' :idField='gresource.idField'  :colorField='gresource.colorField'>
                                    </e-resource>
                                    </template> -->                                    
                                    <template v-for="(resource, index) in ResourceDataSource">
                                    <e-resource :key="index" :field='resource.field' :title='resource.title' :name='resource.name' :allowMultiple='resource.allowMultiple' :dataSource='resource.dataSource'
                                         :textField='resource.textField' :idField='resource.idField' :groupIDField= 'resource.groupIDField' :colorField='resource.colorField'>
                                    </e-resource>
                                    </template>     
                                </e-resources>
                            </ejs-schedule>
                        </div>                        
                        <div class="treeview-container px-3">
                            <div class="title-container">
                                <h1 class="title-text d-flex align-items-center">
                                    <span class="w-100 text-left">{{ $t('UnscheduledList') }}</span>
                                    <span class="select-in-tag">
                                        <input v-model="txtSearchAppointmentNo" class="form-control input-sm"
                                            placeholder="Search by Appt. no." type="text" style="margin-right:100px;"
                                            @input="GetUnscheduleList($event, 'keypress')">
                                    </span>
                                </h1>
                            </div>
                            <div class="treeview-component" v-if="UnSchduleList.length > 0">
                                <ejs-treeview id='Tree' ref='TreeViewScheduleObj' cssClass="treeview-external-drag"
                                    :nodeTemplate="treeTemplate" :fields='treeViewFields'
                                    :allowDragAndDrop='allowDragAndDrop' :nodeDragStop="onTreeDragStop"
                                    :nodeDragging="onTreeDrag" :nodeDragStart="onTreeDragStart"
                                    :nodeSelecting="onItemSelecting">
                                </ejs-treeview>
                            </div>
                            <div v-else class="text-danger text-center py-4 border">{{ $t('NoRecordfound') }}</div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <SchduleAppointment v-if="IsSchduleAppointment" :SchduleAppointment="SchduleAppointment"
            :SA_GroupData="SA_GroupData" :IsCurrentView="CurrentView"></SchduleAppointment>
        <OpenFilter v-if="IsApplyFilter" :CurrentView="CurrentView" :IsDefaultView="IsDefaultView"></OpenFilter>
        <CrewMemberListPopup v-if="IsCrewMemberList" :IsCrewListId="IsCrewListId"></CrewMemberListPopup>
    </div>
</template>
<script>
import { ScheduleComponent, ViewDirective, ViewsDirective, ResourceDirective, ResourcesDirective, Week, Month, Agenda, TimelineViews, TimelineMonth } from "@syncfusion/ej2-vue-schedule";
import { TreeViewComponent } from "@syncfusion/ej2-vue-navigations";
import { addClass, closest } from '@syncfusion/ej2-base';
import TreeVue from './TreeView.vue'
import SchduleAppointment from './SchduleAppointment.vue'
import ToolTip from './ToolTipTemplate.vue'
import EventTemplate from './eventTemplate.vue'
import OpenFilter from './FilterPopUp.vue'
import DataService from '../../services/DataService';
import dateHeaderTemplate from './DateHeader.vue'
import CrewMemberListPopup from "./CrewMemberListPopup.vue";
export default {
    components: {
        'ejs-schedule': ScheduleComponent,
        'e-view': ViewDirective,
        'e-views': ViewsDirective,
        'e-resource': ResourceDirective,
        'e-resources': ResourcesDirective,
        'ejs-treeview': TreeViewComponent,
        TreeVue, EventTemplate,
        SchduleAppointment, ToolTip, OpenFilter,CrewMemberListPopup
    },
    props: {
        IsDefaultView: {
            type: String,
            required: true
        }
    },
    data() {
        return {
            isZoneDisplayNameId:null,
            UserTimeZone: null,
            rowAutoHeight: true,
            isLoading: false,
            IsApplyFilter: false,
            txtSearchAppointmentNo: '',
            isLocateOrNonLocate: '',
            SA_GroupData: [],
            IsSchduleAppointment: false,
            SchduleAppointment: {},
            ResourceDataSource: [],
            selectedDate: new Date(),
            eventSettings: {
                dataSource: [],
                enableTooltip: true,
                editFollowingEvents: true,
                enableIndicator: false,
                enableMaxHeight: true,
                tooltipTemplate: function (e) {
                    return {
                        template: ToolTip
                    };
                },
                eventTemplate: function (e) {
                    return {
                        template: EventTemplate
                    };
                },
            },
            cssClass: 'schedule-drag-drop',
            isZoneDisplayName: '',
            isZoneDisplayNamee: '',           
            ZoneDisplayNameValue:'',
            isTimeZoneEdit: true,
            ExtraMapFeature: false,
            CurrentView: 'mixed',
            resourceListData: [],
            ZoneDisplayName: [],
            UnSchduleList: [],
            ddlCurrentView: [
                { value: 'mixed', name: 'View all' },
                { value: 'crew', name: 'Crew View' },               
                { value: 'resource', name: 'Resource View' }
            ],
            IsView: 'TimelineWeek',
            group: {
                enableCompactView: true,
                resources: ['Employees']
            },
            allowMultiple: true,
            treeViewFields: {
                dataSource: [],
                id: 'SA_Id',
                text: 'Name'
            },
            treeTemplate: function (e) {
                return { template: TreeVue };
            },
            draggedItemId: null,
            _startDate: '',
            _lastDate: '',
            startDate: new Date(),
            lastDate: new Date(),
            allowDragAndDrop: false,
            isTreeItemDropped: false,
            dateHeaderTemplate: function (e) {
                return {
                    template: dateHeaderTemplate
                };
            },
            IsActive: false,
            IsCrewMemberList:false,
            IsCrewListId:null,
            GroupData1:[]
        }
    },
    provide: {
        schedule: [Agenda, TimelineViews, TimelineMonth]
    },
    created: async function () {                
        this.UserTimeZone = this.GetUserInfo.TimeZone;
        await this.IsExtraMapFeature();
        await this.GetUserTimeZoneDisplayName();               
        await this.refreshGhanttChart(); 
    },    
    methods: {
        onEventClick(args) {
            args.cancel = true;
            if (args.event.SA_Status != "Canceled") {
                                this.openScheduledAppointmentPopupForEdit(args.event.Id);
            }
            else {
                this.ShowAlert(this.$t('CancelledSA', [this.$t('ServiceAppointment')]), "Success", true, this.$t('Alert'));
            }
        },
        onEventRendered(args) {                   
            if (args.data.Color != null && args.data.Color != 'null' && typeof args.data.Color != 'undefined') {
                args.element.style.backgroundColor = args.data.Color;
            }            
            for (var xi = 0; xi < document.getElementsByClassName("e-appointment-container").length; xi++) {
                let topSize = 0;
                for (var yi = 0; yi < document.getElementsByClassName("e-resource-cells").length; yi++) {
                    if (xi > yi) {
                        topSize = topSize + document.getElementsByClassName("e-resource-cells")[yi].offsetHeight;
                    } else {
                        break;
                    }
                }                
                document.getElementsByClassName("e-appointment-container")[xi].style.top = topSize + "px";
            }

        },
        openScheduledAppointmentPopupForEdit(val) {            
            var url = `id=${val}&isForTickets=${this.ExtraMapFeature}`
            DataService.GetSADetailById(url).then(response => {  
                var data = response.data.result;
                this.openScheduledAppointmentPopup(data);
            });
        },
        onAppointmentClick: function (args) {
            args.cancel = true;
        },
        ChangeViewResource() {
            this.refreshGhanttChart();
        },
        ApplyFilter() {
            this.IsApplyFilter = true;
        },
        UnLockUnSchduleList() {
            this.$refs.ancLock.classList.remove('e-lib');
            this.$refs.ancLock.classList.remove('e-btn');
            this.$refs.ancLock.classList.remove('e-active');
            this.$refs.ancLock.classList.remove('e-control');
            this.$refs.ancLock.classList.value = 'btn form-btns mr-2 e-active btn-dark';
            if (this.IsActive == true) {
                this.$refs.ancLock.innerHTML = '<span><i class="fa fa-lock p-0"></i>';
                this.$refs.ancLock.classList.add('btn-success');
                this.$refs.ancLock.classList.remove('btn-dark');
                this.$refs.ancLock.classList.remove('e-active');
                this.allowResizing = false;
                this.allowDragAndDrop = false;
                this.IsActive = false;
            } else {
                this.$refs.ancLock.innerHTML = '<span><i class="fa fa-unlock p-0"></i>';
                this.$refs.ancLock.classList.remove('btn-success');
                this.$refs.ancLock.classList.add('btn-dark');
                this.$refs.ancLock.classList.add('e-active');
                this.allowResizing = true;
                this.allowDragAndDrop = true;
                this.IsActive = true;

            }
        },
        RefreshFilter() {
            this.$refs.ancRefresh.classList.remove('e-lib');
            this.$refs.ancRefresh.classList.remove('e-btn');
            this.$refs.ancRefresh.classList.remove('e-control');
            this.CurrentView='mixed';
            this.refreshGhanttChart();
        },
        refreshGhanttChart: async function () {            
            this.isLoading = true;
            var activeDates = this.$refs.ScheduleObj.ej2Instance.activeView.renderDates[0];
            var startDate = this.$options.filters.formatDate(activeDates);
            this.$refs.ScheduleObj.ej2Instance.activeView.renderDates.reverse();
            var activeDates = this.$refs.ScheduleObj.ej2Instance.activeView.renderDates[0];
            var endDate = activeDates.setDate(activeDates.getDate() + 1);
            endDate = this.$options.filters.formatDate(activeDates);
            this._startDate = startDate;
            this._lastDate = endDate;
            await this.GetCrewResourceList();
            await this.GetUnscheduledServiceAppointmentList();
        },
        refreshCalenderData: async function (isOnlyCalender = false) {            
            var ths = this;
            ths.isLoading = true;
            if (isOnlyCalender) {
                var activeDates = this.$refs.ScheduleObj.ej2Instance.activeView.renderDates[0];
                var startDate = this.$options.filters.formatDate(activeDates);
                this.$refs.ScheduleObj.ej2Instance.activeView.renderDates.reverse();
                var activeDates = this.$refs.ScheduleObj.ej2Instance.activeView.renderDates[0];
                var endDate = activeDates.setDate(activeDates.getDate() + 1);
                endDate = this.$options.filters.formatDate(activeDates);
                this._startDate = startDate;
                this._lastDate = endDate;
            }

            var url = `viewType=${this.CurrentView}&currentdate=${this._startDate}&lastdate=${this._lastDate}&isFirstTime=true&filters=&timezone_id=${this.isZoneDisplayNameId}`;
            await DataService.GetSACalenderList(url).then(response => {
                if (response.data.status === "Success") {
                    this.dataCalenderSource = response.data.result;
                    if (this.getCurrentView() === "crew") {
                        var _data = [];
                        this.dataCalenderSource.forEach(m => {
                            if (m.IsCrew == 1) {
                                m = Object.keys(m).filter(function (key) {
                                    return key !== 'EmployeeId';
                                }).reduce(function (obj, key) {
                                    obj[key] = m[key];
                                    return obj;
                                }, {});
                                _data.push(m);
                            }
                            else {
                                _data.push(m);
                            }
                        });
                        ths.eventSettings = { dataSource: [..._data] };
                        ths.$refs.ScheduleObj.ej2Instance.refresh()
                        //ths.isLoading=false;
                    } else {                        
                        ths.eventSettings = { dataSource: [...this.dataCalenderSource] };
                        ths.$refs.ScheduleObj.ej2Instance.refresh()
                        //ths.isLoading=false;
                    }
                    ths.isLoading=false;
                }
            });
            
        },
        onTreeDrag: function (event) {
            let scheduleObj = this.$refs.ScheduleObj.ej2Instances;
            if (scheduleObj.isAdaptive) {
                let classElement = document.querySelector('.e-device-hover');
                if (classElement) {
                    classElement.classList.remove('e-device-hover');
                }
                if (event.target.classList.contains('e-work-cells')) {
                    addClass([event.target], 'e-device-hover');
                }
            }
        },
        dataFilter: function (item) {
            return item.Id !== parseInt(this.draggedItemId, 10);
        },
        onActionBegin: function (event) {
            if (event.requestType === 'eventCreate' && this.isTreeItemDropped) {
                let treeObj = document.getElementById('Tree').ej2_instances[0];
                let treeViewdata = treeObj.fields.dataSource;
                let filteredPeople = treeViewdata.filter(this.dataFilter);
                treeObj.fields.dataSource = filteredPeople;
                let elements = document.querySelectorAll('.e-drag-item.treeview-external-drag');
                for (let i = 0; i < elements.length; i++) {
                    remove(elements[i]);
                }
            }
        },
        onItemSelecting: function (args) {
            args.cancel = true;
        },
        onTreeDragStop: function (event) {            
            let treeElement = closest(event.target, '.e-treeview');
            let classElement = document.querySelector('.e-device-hover');
            if (classElement) {
                classElement.classList.remove('e-device-hover');
            }
            if (!treeElement) {
                event.cancel = true;
                let scheduleElement = closest(event.target, '.e-content-wrap');
                if (scheduleElement) {
                    let treeObj = document.getElementById('Tree').ej2_instances[0];
                    let treeviewData = treeObj.fields.dataSource;
                    if (event.target.classList.contains('e-work-cells')) {
                        let filteredData =
                            treeviewData.filter(function (item) {
                                return item.SA_Id === parseInt(event.draggedNodeData.id, 10);
                            });
                        let scheduleObj = document.getElementById('Schedule').ej2_instances[0]
                        let cellData = scheduleObj.getCellDetails(event.target);
                        let data = scheduleObj.getResourcesByIndex(cellData.groupIndex);
                        this.draggedItemId = event.draggedNodeData.id;
                        this.isTreeItemDropped = true;
                        var _endTime, _startTime;
                        if (typeof cellData.startTime == 'undefined') {
                            _startTime = new Date();
                        } else {
                            _startTime = cellData.startTime;
                        }

                        if (typeof cellData.endTime == 'undefined') {
                            _endTime = new Date();
                        } else {
                            _endTime = cellData.endTime;
                        }
                        var groupData = [];
                        if (this.getCurrentView() == 'crew') {
                            groupData = data.resourceData.Id.split('-');
                        } else {
                            groupData = data.groupData.EmployeeId.split('-');
                        }
                        var groupId = groupData[0];
                        var groupType = groupData[1];
                        var groupName = data.resourceData.Text + ' ' + '(' + data.resourceData.Designation + ')';

                        if (groupId == '' || groupId == null) {
                            groupId = '-1';
                        }

                        let eventData = {
                            SA_Number: filteredData[0].Name,
                            SA_Account: filteredData[0].AccountName,
                            SA_Scheduled_StartTime: _startTime,
                            SA_Scheduled_EndTime: _endTime,
                            SA_Subject: filteredData[0].Subject,
                            SA_Account_Id: filteredData[0].AccountId,
                            SA_Account_Id: filteredData[0].AccountId,
                            SA_Id: filteredData[0].SA_Id,
                            SA_WorkOrder: filteredData[0].WorkOrder,
                            SA_WorkType: filteredData[0].WorkType,
                            SA_Description: filteredData[0].SA_Description,
                            groupId: groupId,
                            groupType: groupType,
                            groupName: groupName,
                            RelatedObjectId: filteredData[0].RelatedObjectId,
                            TicketNumber: filteredData[0].TicketNumber,
                            AssignedResources: '',
                            AssignedCrews: ''
                        };
                        this.openScheduledAppointmentPopup(eventData);
                    }
                }
            }
            document.body.classList.remove('e-disble-not-allowed')
        },
        onTreeDragStart: function () {
            document.body.classList.add('e-disble-not-allowed');
        },
        getCurrentView: function () {
            try {
                var value = localStorage.getItem('view');
                return (value != null) ? value : 'resource';
            }
            catch {
                return 'resource';
            }
        },
        openScheduledAppointmentPopup: function (e) {            
            if (e) {
                if (typeof e.groupId != 'undefined' && e.groupId != null) {
                    this.SA_GroupData = [];
                    this.SA_GroupData.push({ value: parseInt(e.groupId), name: e.groupName, type: e.groupType })
                }
                this.SchduleAppointment = e;
                this.IsSchduleAppointment = true;
            }
        },
        GetUnscheduleList: function (e, type) {
            var keyCode = e.keyCode || e.which;
            var value = this.txtSearchAppointmentNo.toLowerCase();
            if (keyCode == 35 || keyCode == 36 || keyCode == 38) {
                this.ShowAlert('Not a valid character.', "warning", this.$t('Alert'));
                this.GetUnscheduledServiceAppointmentList();
            }
            else if (/^[a-zA-Z0-9- ]*$/.test(value) == false) {
                this.ShowAlert('Please enter valid characters.', "warning", this.$t('Alert'));
                this.GetUnscheduledServiceAppointmentList();
            }
            else {
                var value = this.txtSearchAppointmentNo;
                this.GetUnscheduledServiceAppointmentList(value);
            }
        },
        GetUnscheduledServiceAppointmentList: async function (type) {
            var Unschduletype = type == undefined ? '' : type;
            var url = `search=${Unschduletype}`;
            await DataService.GetUnscheduledServiceAppointmentList(url).then(response => {
                this.UnSchduleList = response.data.result;
                if (this.UnSchduleList.length > 0) {
                    var Obj = {
                        dataSource: this.UnSchduleList,
                        id: 'SA_Id', text: 'Name'
                    }
                    this.treeViewFields = Obj;
                }
            });
        },
        dataFilter: function (item) {
            return item.Id !== parseInt(this.draggedItemId, 10);
        },
        getConsultantName: function (data) {
            let value = JSON.parse(JSON.stringify(data));
            if (value.resourceData.IsCrew == "1") {
                if (this.getCurrentView() == "crew") {
                    return value.resourceData[value.resource.textField];
                }
                else {
                    return "<a class='link anccrew' v-on:click=CrewMemeberListPopup(value.resourceData.CrewId) :data-crewid='" + value.resourceData.CrewId + "' >" + value.resourceData[value.resource.textField] + "</a>";
                }
            }
            return value.resourceData[value.resource.textField];
        },
        GetConsultantName(value){            
            if (value.resourceData.IsCrew == "1") {
                if (this.getCurrentView() == "crew") {
                    
                }
                else {
                    this.CrewMemeberListPopup(value.resourceData.CrewId)
                }
            }
        },
        CrewMemeberListPopup(ths) {            
            this.IsCrewMemberList=true;
            this.IsCrewListId=ths;
        },
        getConsultantImageName: function (value) {
            var imgUrl = value.resourceData.profilepic;
            var id = parseInt(value.resourceData.Id);
            if (id == -1) {
                return '<span class="sa_unassigned"><i class="specialist-img d-flex justify-content-center align-items-center fa fa-user-times" aria-hidden="true"></i></span>';
            }
            else {
                if (imgUrl === "") {
                    return '';
                } else {                    
                    if (value.resourceData.IsCrew == "1") {
                        return '<span class="sa_unassigned"><i class="specialist-img d-flex justify-content-center align-items-center fa fa-users"></i></span>';
                    } else {
                        return "<img class='specialist-img' src='https://talygenv.github.io/Vue-UI-Content/images/DefaultImages/uc_user_new.jpg' />";
                    }
                }
            }
        },
        getConsultantDesignation: function (data) {
            let value = JSON.parse(JSON.stringify(data));
            var resourceName = value.resourceData[value.resource.textField];
            if (resourceName === "GENERAL" || resourceName === "DENTAL") {
                return '<div class="specialist-designation"></div>';
            } else if (typeof value.resourceData.Designation != 'undefined' && value.resourceData.Designation != "") {
                //return value.resourceData.Designation;
                return '<div class="specialist-designation">' + value.resourceData.Designation + '</div>';
            }
            else {
                return '';
            }
        },
        onRenderCell(args) {
            if (args.elementType === 'emptyCells' && args.element.classList.contains('e-resource-left-td')) {
                var target = args.element.querySelector('.e-resource-text');
                if (this.CurrentView == "crew") {
                    target.innerHTML = `<div class="name"  >Crew Name </div>`;
                } else if (this.CurrentView == "customer") {
                    target.innerHTML = `<div class="name"  >Customer Name </div>`;
                }
                else if (this.CurrentView == "mixed") {
                    target.innerHTML = `<div class="name"  >Crew/Resource Name </div>`;
                }
                else {
                    target.innerHTML = `<div class="name"  >Resource Name </div>`;
                }
            }
        },
        getCurrentViewTimeline() {
            try {
                var value = localStorage.getItem('timeline');
                return (value != null) ? value : 'Week';
            }
            catch {
                return 'Week';
            }
        },
        onActionComplete(args) {
            var ths = this;
            if (args.requestType == 'viewNavigate' || args.requestType == 'dateNavigate') {
                var instances = [...document.getElementById('Schedule').ej2_instances];
                var instances = instances.reverse();
                var scheduleobj = instances[2];
                var dateSet = [...scheduleobj.getCurrentViewDates()];
                if (dateSet.length > 0) {
                    var startDate = dateSet[0];
                    var lastDate = dateSet.reverse()[0];
                    if (ths.getCurrentViewTimeline() == 'Day') {
                        lastDate = new Date(lastDate.setDate(lastDate.getDate() + 1));
                    }
                }

                this.IsView = 'Timeline' + this.getCurrentViewTimeline();
                this._startDate = this.startDate.toLocaleDateString();
                this._lastDate = lastDate.toLocaleDateString();
                this.selectedDate = this.startDate;
                this.refreshGhanttChart();
            }
        },
        GetUserTimeZoneDisplayName: async function () {                      
            await DataService.GetTimeZoneByTimeZone().then(response => {
                if(response.data.status=="Success"){                    
                    var TimeZoneData=response.data.result;
                    this.isZoneDisplayNamee = TimeZoneData[0].display_name;
                    this.isZoneDisplayNameId=TimeZoneData[0].timezone_id;
                }                
            });
        },
        GetTimeZoneList: function () {     
            var vm = this;
            DataService.GetTimeZoneListing().then(function (response) {               
                if (response.data.result.length > 0) {                    
                    vm.isZoneDisplayName = response.data.result;                                        
                }
            });
        },

        IsExtraMapFeature: async function () {
            await DataService.IsExtraMapFeature().then(response => {
                if (response.data == 'locating') {
                    this.ExtraMapFeature = true;
                    this.removeViewWithValue(this.ddlCurrentView, this.ExtraMapFeature);
                }
                else {
                    this.ExtraMapFeature = false;
                }
            });
        },
        removeViewWithValue(arr, id) {
            if (id) {
                const objWithIdIndex = arr.findIndex((obj) => obj.value === 'customer');
                if (objWithIdIndex > -1) {
                    arr.splice(objWithIdIndex, 1);
                }
                return arr;
            }
        },        
        TimeZoneEdit() { 
        // debugger
            this.isTimeZoneEdit = !this.isTimeZoneEdit;         
            this.GetTimeZoneList();        
        },
        ddlTimeZone() {       
            debugger      
            var vm=this;
            var timeZoneData=this.isZoneDisplayName.find(function (element) {
                return element.VALUE==vm.isZoneDisplayNameId
            });
            vm.isZoneDisplayNamee=timeZoneData.TEXT;
            this.isZoneDisplayNameId=timeZoneData.VALUE;
            vm.isTimeZoneEdit = !vm.isTimeZoneEdit; 
            vm.refreshGhanttChart();         
        },
        setCurrentView(value) {
            localStorage.setItem('view', value);
        },
        GetCrewResourceList: async function () {
            var vm = this;
            var type = vm.CurrentView;
            var url = `type=${type}&startTime=${this._startDate}&endTime=${this._lastDate}`;
            await DataService.GetCrewResourceList(url).then(response => {
                let resources = [];
                let dataResources = [];
                //this.hideUnscheduledList();
                this.setCurrentView(type);
                vm.resourceListData = response.data.result;
                if (response.data.status == "Success") {
                    if (type != "customer") {
                        //vm.showUnscheduledList();
                    }
                    if (type === "resource" || type === "mixed") {
                        resources = ['Employees'];
                        dataResources.push({
                            field: "EmployeeId",
                            title: "Employee",
                            name: "Employees",
                            allowMultiple: true,
                            textField: 'Text',
                            idField: 'Id',
                            colorField: 'Color',
                            dataSource: vm.resourceListData,
                        })
                    }
                    else if (type === "crew") {
                        resources = ["Groups", "Employees"];
                        dataResources.push({
                            field: "EmployeeId",
                            title: "Employee",
                            name: "Employees",
                            textField: 'Text',
                            idField: 'Id',
                            groupIDField: 'GroupId',
                            colorField: 'Color',
                            allowMultiple: false,
                            dataSource: vm.resourceListData["dataResources"]
                        });
                        vm.GroupData1.push({
                            field: "Group_Id",
                            title: "Group",
                            name: "Groups",
                            textField: 'Text',
                            idField: 'Gid',
                            colorField: 'Color',
                            dataSource: vm.resourceListData["dataGroup"]
                        })
                    }
                    else if (type === "customer") {
                        resources = ['Customers'];
                        dataResources.push({
                            field: "CustomerId",
                            title: "Customer",
                            name: "Customers",
                            allowMultiple: false,
                            textField: 'Text',
                            idField: 'Id',
                            colorField: 'Color',
                            dataSource: vm.resourceListData
                        });
                    }                    
                    vm.group.resources = resources;
                    vm.ResourceDataSource = dataResources;
                    vm.refreshCalenderData();
                }
                vm.isLoading = false;
            });
        }
    }
}
</script>
