<template>
    <div class="my-popups">
        <loader :is-visible="isLoading"></loader>
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header theme-primary partition-full d-flex">
                        <h4 class="modal-title">{{ $t('Filter') }}</h4>
                        <button type="button" class="close" v-on:click="Close">
                            <em class="fa fa-times" aria-hidden="true"></em>
                        </button>
                    </div>

                    <div class="modal-body">
                        <dynamic-form lang="en" :buttons="buttons" :schema="FormSchema"
                            v-on:OnSubmit="onSubmit"></dynamic-form>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import DataService from '../../services/DataService';
export default {
    props: {
        CurrentView: {
            type: String,
            required: true
        },
        IsDefaultView: {
            type: String,
            required: true
        }
    },
    data() {
        return {
            isLoading: false,
            FormSchema: [
                {
                    layoutType: "double",
                    group_name: 'Filter in Resource and Unscheduled Appointment List',
                    Data: [
                        {
                            astype: "SelectField",
                            label: this.$t('WorkType'),
                            name: "WorkType",
                            value: "",
                            placeholder: "",
                            config: {
                                options: []
                            },
                            validationRules: { "required": false, }
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('Account'),
                            name: "Account",
                            value: "",
                            placeholder: "",
                            config: {
                                options: []
                            },
                            validationRules: { "required": false, }
                        },
                    ]
                },
                {
                    layoutType: "double",
                    group_name: 'Filter in Service Resource',
                    Data: [
                        {
                            astype: "SelectField",
                            label: this.$t('Department'),
                            name: "Department",
                            value: "",
                            placeholder: "",
                            config: {
                                options: []
                            },
                            validationRules: { "required": false, }
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('ResourceName'),
                            name: "ResourceName",
                            value: "",
                            placeholder: "",
                            config: {
                                options: []
                            },
                            validationRules: { "required": false, }
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('CrewName'),
                            name: "CrewName",
                            value: "",
                            placeholder: "",
                            config: {
                                options: []
                            },
                            validationRules: { "required": false, }
                        },
                    ]
                }
            ],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-search text-white pr-2'></i>Save and Search",
                },
                {
                    type: "submit",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-refresh text-white pr-2'></i>Reset"
                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-times text-white pr-2'></i>Close",
                    onClick: this.Close
                }
            ],
        }
    },
    created: function () {
        this.GetWorkTypeDDL();
        this.GetAccountDDL();
        this.GetDepartmentDDL();
        this.GetResourceDDL();
        this.GetCrewDDL();
        this.GetFilterDDLSelectedValue();
    },
    methods: {
        GetWorkTypeDDL() {
            var url = `type=worktype`
            DataService.getStatusList(url).then(response => {
                var WorkTypeArrary = [];
                WorkTypeArrary = response.data.result;
                WorkTypeArrary.forEach(element => {
                    let obj = {
                        name: element.text,
                        value: element.value
                    }
                    this.FormSchema[0].Data[0].config.options.push(obj);
                });
            })
        },
        GetAccountDDL() {
            var url = `type=account`
            DataService.getStatusList(url).then(response => {
                var WorkTypeArrary = [];
                WorkTypeArrary = response.data.result;
                WorkTypeArrary.forEach(element => {
                    let obj = {
                        name: element.text,
                        value: element.value
                    }
                    this.FormSchema[0].Data[1].config.options.push(obj);
                });
            })
        },
        GetDepartmentDDL() {
            var url = `type=department`
            DataService.getStatusList(url).then(response => {
                var WorkTypeArrary = [];
                WorkTypeArrary = response.data.result;
                WorkTypeArrary.forEach(element => {
                    let obj = {
                        name: element.text,
                        value: element.value
                    }
                    this.FormSchema[1].Data[0].config.options.push(obj);
                });
            })
        },
        GetResourceDDL() {
            var url = `type=resource`
            DataService.getStatusList(url).then(response => {
                var WorkTypeArrary = [];
                WorkTypeArrary = response.data.result;
                WorkTypeArrary.forEach(element => {
                    let obj = {
                        name: element.text,
                        value: element.value
                    }
                    this.FormSchema[1].Data[1].config.options.push(obj);
                });
            })
        },
        GetCrewDDL() {
            var url = `type=crew`
            DataService.getStatusList(url).then(response => {
                var WorkTypeArrary = [];
                WorkTypeArrary = response.data.result;
                WorkTypeArrary.forEach(element => {
                    let obj = {
                        name: element.text,
                        value: element.value
                    }
                    this.FormSchema[1].Data[2].config.options.push(obj);
                });
            })
        },
        Close() {
            this.$parent.IsApplyFilter = false;
        },
        onSubmit: function (formfields, val) {
            //debugger
            this.isLoading = true;
            var JsonObj = {
                view: this.IsDefaultView,
                gantt_view_type: this.CurrentView,
                filter_data: val.split('/i>')[1] == 'Reset' ? null : {
                    worktype: formfields.WorkType,
                    account: formfields.Account,
                    department: formfields.Department,
                    resource: formfields.ResourceName,
                    crew: formfields.CrewName
                },
                isFirstTime: true
            };
            var obj = JSON.stringify(JsonObj);
            DataService.SaveAndSearchGanttFilter(obj).then(response => {
                if (response.data.status == "Success") {
                    this.$parent.refreshGhanttChart();
                    this.Close();
                    if (val.split('/i>')[1] == 'Reset') {
                        this.ShowAlert(this.$t("FilterDataClearSuccessfully"), "success", true, this.$t("Alert"));
                    }
                    else {
                        this.ShowAlert(this.$t("FilterDataAppliedSuccessfully"), "success", true, this.$t("Alert"));
                    }
                    this.isLoading = false;
                }
            })
        },
        GetFilterDDLSelectedValue() {
            var vm = this;
            DataService.GetFilterDDLSelectedValue().then(response => {
                var filterData = JSON.parse(response.data.filterData);                
                if (filterData !=null) {
                    vm.FormSchema[0].Data[0].value = filterData['account'];
                    vm.FormSchema[0].Data[0].value = filterData['worktype'];
                    vm.FormSchema[1].Data[0].value = filterData['department'];
                    vm.FormSchema[1].Data[1].value = filterData['resource'];
                    vm.FormSchema[1].Data[2].value = filterData['crew'];
                }
            });
        }
    }
}
</script>