<template>
    <section>
        <loader :is-visible="isLoading"></loader>
        <div class="row" v-if="!IsListView">
            <div class="col-md-12">
                <div class="theme-primary partition p-actions-expand">
                    <span class="p-name text-white">{{ $t('ServiceAppointment') }}</span>
                    <span class="p-actions float-right">
                        <a id="ancListView" @click="loadView('List')" :title="$t('ListView')"
                            class="p-action-btn text-white" data-toggle-tooltip="tooltip"
                            :data-original-title="$t('ListView')"><em class="fa fa-list"></em><br>
                            {{ $t('ListView') }}</a>
                        <a id="ancSettingView" @click="SetDefaultView()" :title="$t('SetDefaultView')"
                            class="p-action-btn text-white ancSettingView" data-toggle-tooltip="tooltip"
                            :data-original-title="$t('SetDefaultView')"><em class="fa fa-cog"></em><br>
                            {{ $t('SetDefaultView') }}</a>
                    </span>
                </div>
            </div>
        </div>
        <section v-if="IsListView">
            <tg-list  ref="tgList" :IsShowAction="true" ModuleName="Ticketing" SubModuleCode="SERVICE_APPOINTMENT" :LegendArray="[]"
                :RenderRowActionMethod="renderActions" v-on:ActionButtonClick="actionButtonClick" :widgets="widgets"
                IdentityColumn="SA_Id" :HeaderText="$t('ServiceAppointment')" :listType="['List']" :ListData="SAData"
                :HeaderData="Headers" :HeaderButtons="listheaderbuttons" :TotalRecords="TotalRecords"  v-on:ApplyView="OnApplyView" :ViewList="ViewList"
                :SearchFields="leftSearchFields" :ListDataCallBackFunction="fetchRecords" :showCheckBox="true"
                :SortExp="SortExp" :SortBy="SortBy" :NorecordfoundText="$t('NoRecordfound')"
                v-on:LeftsearchButtonClick="leftCommonSearch" v-on:PagerButtonClick="pagerMethod"
                v-on:HeaderButtonClick="commonHeaderButtonClick" v-on:SortdataButtonClick="sortdata">
                <template v-slot:slotdata="{ data }">
                    <template v-if="data.column.COLUMN_NAME == 'Status'">                       
                        <a class="active">
                            <span v-bind:class="getStatusClassList(data.row.Status, 'Status')">
                                {{ data.row[data.column.COLUMN_NAME] }}
                            </span>
                        </a>
                    </template>
                </template>
            </tg-list>
            <SearchFilter v-if="showFilter" submodulecode="SERVICE_APPOINTMENT" modulename="Ticketing"
                v-on:close="showFilter = false" v-bind:callbackfunction="fetchRecords" v-on:FilterData="FilterData">
                <h5 slot="header">{{ $t('Search') }}</h5>
            </SearchFilter>
            <ManageView v-if="ShowManageView" submodulecode="SERVICE_APPOINTMENT" modulename="Ticketing"
                v-on:close="ShowManageView = false" v-bind:callbackfunction="fetchRecords" :usertype="usertype">
                <h5 slot="header">{{ $t('Search') }}</h5>
            </ManageView>
            <OpenSetDefaultViewModal v-if="OpenSetDefaultView"></OpenSetDefaultViewModal>
            <SchduleAppointment v-if="IsSchduleAppointment" :SchduleAppointment="SchduleAppointment"
                :SA_GroupData="SA_GroupData"></SchduleAppointment>
        </section>
        <section v-if="!IsListView">
            <ViewSchduler :IsDefaultView="IsDefaultView"></ViewSchduler>
        </section>
        <OpenSetDefaultViewModal v-if="OpenSetDefaultView"></OpenSetDefaultViewModal>
        <AddServiceAppointmentPopUpVue v-if="showPopupAddServiceAppointment" :SchduleObj="SchduleObj" :callfrom="ServiceAppointment"></AddServiceAppointmentPopUpVue>
    </section>
</template>
<script>
import Schema from "../ServiceAppointment/ServiceAppointmentSchema";
import SearchFilter from '../../components/Common/search-filter.vue'
import ManageView from '../../components/Common/view-layout.vue'
import OpenSetDefaultViewModal from '../../components/Modal/OpenSetDefaultViewModal.vue'
import DataServices from "@/services/DataService";
import ViewSchduler from './Scheduler.vue';
import SchduleAppointment from './SchduleAppointment.vue';
import AddServiceAppointmentPopUpVue from '../WorkOrder/AddServiceAppointmentPopUp.vue';
export default {
    components: { SearchFilter, ManageView, OpenSetDefaultViewModal, ViewSchduler, SchduleAppointment ,AddServiceAppointmentPopUpVue},
    data() {
        return {
            isLoading: false,
            ServiceAppointment:'ServiceAppointment',
            showPopupAddServiceAppointment:false,
            IsSchduleAppointment: false,
            listheaderbuttons: Schema.buttons,
            leftSearchFields: Schema.leftsearchSchema,
            listActions: Schema.listActions,
            TotalRecords: 0,
            PageNumber: 1,
            CurrentPage: 1,
            PageSize: 10,
            isPaged: 1,
            SortBy: '',
            SortExp: '',
            TotalPages: 0,
            IsFsmLocating: false,
            selectedWidgetCode: '',
            searchCondition: '',
            SAData: [],
            Headers: [],
            widgets: [
                {
                    icon: "fa fa-list-ul",
                    value: "",
                    name: "Completed",
                    class: "one_wid",
                    onClick: this.widgetClick
                },
                {
                    icon: "fa fa-ticket",
                    value: "",
                    name: "In progress",
                    class: "two_wid",
                    onClick: this.widgetClick
                },
                {
                    icon: "fa fa-line-chart",
                    value: "",
                    name: "Need To Reschedule",
                    class: "three_wid",
                    onClick: this.widgetClick
                },
                {
                    icon: "fa fa-reply",
                    value: "",
                    name: "Scheduled",
                    class: "four_wid",
                    onClick: this.widgetClick
                },
                {
                    icon: "fa fa-calendar-check-o",
                    value: "",
                    name: "Unscheduled",
                    class: "five_wid",
                    onClick: this.widgetClick
                },
                {
                    icon: "fa fa-file-text",
                    value: "",
                    name: "Cancelled",
                    class: "six_wid",
                    onClick: this.widgetClick
                }
            ],
            UserType: null,
            showFilter: false,
            ShowManageView: false,
            OpenSetDefaultView: false,
            IsDefaultView: '',
            IsListView: false,
            appliedFilter: [],
            ExtraMapFeature: false,
            SchduleAppointment: {},
            SA_GroupData: [],            
            ViewList:[],
            ViewListId:'',
            SchduleObj:{}
        }
    },
    created: async function () {
        this.UserType = this.GetUserInfo.UserType;
        await this.IsExtraMapFeature();
        await this.GetDefaultView();
        await this.loadView(this.IsDefaultView);
        await vm.fetchRecords()
        await vm.GetDdlRecords_ViewList();
        await Schema.bindleftCommonSearchdropdown(this, "Ticketing", "FilterACCOUNTSListForServiceAppointment");      
    },
    methods: {
        OnApplyView(viewId) {
            var vm = this;
            if (viewId == '0') {
                vm.DefaultView()
            }
            else {
                var obj = {
                    ViewId: viewId,
                    ModuleName: 'Ticketing',
                    SubmoduleCode: 'SERVICE_APPOINTMENT'
                };
                var objectPostString = JSON.stringify(obj);
                DataServices.ApplyView(objectPostString).then(function (response) {
                    if (response.data != null) {
                        if (response.data.code == "1" || response.data.Code == "1") {
                            vm.ShowAlert(vm.$t("ApplySuccess", [vm.$t("View")]), 'success', true, vm.$t('Alert'));
                            vm.$emit('RefreshManageView');
                            vm.fetchRecords();
                        }
                        else {
                            vm.ShowAlert(vm.$t("AddedError", [vm.$t("View")]), 'failure', true, vm.$t('Alert'));
                        }
                    }
                    else {
                        vm.ShowAlert(vm.$t("AddedError", [vm.$t("View")]), 'failure', true, vm.$t('Alert'));
                    }
                });

            }
        },
        DefaultView: function () {  
            var vm = this;
            var obj = {
                ModuleName: 'Ticketing',
                SubmoduleCode: 'SERVICE_APPOINTMENT'
            };
            var objectPostString = JSON.stringify(obj);
            DataServices.ClearView(objectPostString).then(function (response) {
                if (response.data != null) {
                    if (response.data.code == "1" || response.data.Code == "1") {
                        vm.ShowAlert(vm.$t("ClearSuccess", [vm.$t("DefaultView")]), 'success', true, vm.$t('Alert'));
                        vm.$emit('RefreshManageView');
                        vm.fetchRecords();
                    }
                    else {
                        vm.ShowAlert(vm.$t("ErrorWhileProcessingRequest"), 'failure', true, vm.$t('Alert'));
                    }
                }
                else {
                    vm.ShowAlert(vm.$t("ErrorWhileProcessingRequest"), 'failure', true, vm.$t('Alert'));
                }
            });
        },
        GetDdlRecords_ViewList: async function () {            
            var vm = this;
            vm.ViewList=[];
            var url = `moduleName=Ticketing&subModuleCode=SERVICE_APPOINTMENT&viewId=0&pageSize=10&pageNum=1&sortBy=&sortExp=`;
            await DataServices.GetViewList(url).then(function (response) {                
                if(response.data && typeof(response.data.DATA) != 'undefined'){
                    vm.ViewList = response.data.DATA;
                }
                var chkVACTIVE = 0;
                vm.ViewListId = "0";
                if (vm.ViewList == null || vm.ViewList == "") {
                   
                }
                else {
                    vm.ViewList.forEach(function (item) {

                        if (item.IS_SLECTED_USER_ID > 0) {
                            chkVACTIVE = chkVACTIVE + 1;
                            vm.ViewListId = item.VIEW_ID;
                        }
                    });
                }
                var indx = 0;
                if (chkVACTIVE > 0) {
                    vm.ViewList.splice(indx, 0, { "SUB_MODULE_NAME": "", "SUB_MODULE_CODE": "", "VIEW_NAME": "Default View", "VIEW_ID": 0, "IS_APPLICABLE_TO_ALL": 1, "MODULE_NAME": "", "CREATED_AT": "", "IS_SLECTED_USER_ID": 0, "CAN_EDIT": 0, "ROWNUMBER": 0, "VIEW_TYPE": "", "USER_LIST": "" });
                }
                else {
                    if (vm.ViewList == null || vm.ViewList == "") {

                        vm.ViewList.push({ "SUB_MODULE_NAME": "", "SUB_MODULE_CODE": "", "VIEW_NAME": "Default View", "VIEW_ID": 0, "IS_APPLICABLE_TO_ALL": 1, "MODULE_NAME": "", "CREATED_AT": "", "IS_SLECTED_USER_ID": 1, "CAN_EDIT": 0, "ROWNUMBER": 0, "VIEW_TYPE": "", "USER_LIST": "" });
                    }
                    else {
                        vm.ViewList.splice(indx, 0, { "SUB_MODULE_NAME": "", "SUB_MODULE_CODE": "", "VIEW_NAME": "Default View", "VIEW_ID": 0, "IS_APPLICABLE_TO_ALL": 1, "MODULE_NAME": "", "CREATED_AT": "", "IS_SLECTED_USER_ID": 1, "CAN_EDIT": 0, "ROWNUMBER": 0, "VIEW_TYPE": "", "USER_LIST": "" });

                    }
                }
                setTimeout(() => {
                    if( vm.ViewListId > 0){
                        vm.$refs.tgList.$refs.tgListHeaderButtons.ViewListId = vm.ViewListId;    
                    }else{
                        vm.$refs.tgList.$refs.tgListHeaderButtons.ViewListId = "0";
                    }
                }, 1000);              
            });           
        },
        IsExtraMapFeature: async function () {
            await DataServices.IsExtraMapFeature().then(response => {
                if (response.data == 'locating') {
                    this.ExtraMapFeature = true;
                }
                else {
                    this.ExtraMapFeature = false;
                }
            });
        },
        getStatusClassList: function (status) {
            var returnClass = '';
            switch (status) {
                case 'Completed':
                case 'Dispatched':
                    returnClass = 'px-2 bg-success text-white text-center d-block';
                    break;
                case 'Cancelled':
                case 'Canceled':
                    returnClass = 'px-2 bg-danger text-white text-center d-block';
                    break;
                case 'Cannot Complete':
                case 'Open':
                case 'Unscheduled':
                    returnClass = 'px-2 bg-secondary text-white text-center d-block';
                    break;
                case 'In Progress':
                    returnClass = 'px-2 bg-info text-white text-center d-block';
                    break;
                case 'Needs Reschedule':
                case 'Scheduled':
                    returnClass = 'px-2 bg-dark text-white text-center d-block';
                    break;
                case 'Failed':
                case 'On Hold':
                case 'Need to Reschedule':
                    returnClass = 'px-2 bg-warning text-white text-center d-block';
                    break;
                default:
                    returnClass = '';
            }
            return returnClass;
        },
        renderActions: function (row) {
            var self = this;
            let rtnActionArray = [];
            self.listActions.forEach(actionItem => {
                let action = { ...actionItem };
                switch (action.title) {
                    case 'View':
                        action.isVisible = true;
                        break;
                    case 'Edit':
                        action.isVisible = this.UserType != 'NU';
                        break;
                    case 'Scheduled Appointment':
                        action.isVisible = true;
                        break;
                    case 'Delete':
                        action.isVisible = true;
                        break;
                    default: {
                        action.isVisible = true;
                    }
                }
                rtnActionArray.push(action);
            });
            return rtnActionArray;
        },
        actionButtonClick(callbackfunction, item, index, event) {
            switch (callbackfunction) {
                case 'ViewDetail':
                    this.openPopupForSetupTickectSource(item.SA_Id)
                    break;
                case 'Edit':
                    this.AddServiceAppointment(item);
                    break;
                case 'ScheduledAppointment':
                    debugger
                    this.openScheduledAppointmentPopupForEdit(item.SA_Id);
                    break;
                case 'SingleDelete':
                    this.commonDeleteData(item.SA_Id);
                    break;
            }
        },
        leftCommonSearch: function (value) {
            this.PageNumber = value.PageNumber;
            this.isPaged = 1;            
            if (typeof value.searchCondition != 'undefined') {
                this.searchCondition = value.searchCondition;
            }
            else {
                this.searchCondition = "";
            }
            this.fetchRecords();
        },
        commonHeaderButtonClick: function (event) {
            switch (event.callbackfunction) {
                case "ManageView":
                    this.ManageView();
                    break;
                case "AddServiceAppointment":
                    this.AddServiceAppointment(null);
                    break;
                case "openFilterPopUp":
                    this.openFilter();
                    break;
                case "removeFilter":
                    var ths = this;
                    ths.searchCondition = '';
                    const removeFilterindex = ths.listheaderbuttons.findIndex(object => {
                        return object.callbackfunction === 'removeFilter';
                    });
                    ths.listheaderbuttons[removeFilterindex].isvisible = false;
                    const openFilterPopUpindex = ths.listheaderbuttons.findIndex(object => {
                        return object.callbackfunction === 'openFilterPopUp';
                    });
                    ths.listheaderbuttons[openFilterPopUpindex].iconClass = 'fa fa-filter';
                    try {
                        ths.appliedFilter = [];
                    } catch (e) { };
                    ths.fetchRecords();
                    break;
                case "GanttChart":
                    this.loadView('Gantt');
                    break;
                case "SetDefaultView":
                    this.SetDefaultView();
                    break;
                case "commonDeleteData":
                    this.commonDeleteData();
                    break;
            }
        },
        SetDefaultView: function () {
            this.OpenSetDefaultView = !this.OpenSetDefaultView;
        },
        pagerMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.fetchRecords();
        },
        FilterData: function (pageNum, pageSize, sortBy, sortExp, searchcondition, filterItemData) {
            this.pageNum = pageNum;
            this.pageSize = pageSize;
            this.sortBy = sortBy;
            this.sortExp = sortExp;
            this.searchCondition = searchcondition;
            if (typeof filterItemData != null)
                this.appliedFilter.push(filterItemData);
            this.fetchRecords();
        },
        sortdata: function (sortBy) {
            this.SortBy = sortBy;
            var sortExp = 'ASC';
            if (this.SortExp == 'ASC') {
                sortExp = 'DESC';
            }
            this.SortExp = sortExp;
            this.SortBy = sortBy;
            this.SortExp = sortExp;
            this.PageNumber = 1;
            this.GroupStart = this.PageNumber;
            this.GroupEnd = this.PageNumber + 2;
            this.fetchRecords();
        },
        clickEventOfColumn: function (row, column) {            
            if (column.COLUMN_NAME == 'SA_Number') {                
                this.$router.push({ path: '/ServiceAppointment/ViewSADetails/' + row.SA_Id ,query: {  TicketNumber: row.SA_Related_Object_Id ,DurationType:row.SA_Duration_Type}});
            }
        },
        columnDataFormatter: function (row, column) {
            return row[column.COLUMN_NAME];
        },
        ManageView: function () {
            var vm = this;
            vm.ShowManageView = true;
            setTimeout(function () {
                $("#modalManageView").modal('show');
                setTimeout(function () {
                    vm.SetBootstraptoolTip();
                }, 200);
            }, 1000);
        },
        getRequestParams: function (pageNum, pageSize, sortBy, sortExp, isPaged, searchcondition) {
            let params = {};
            if (pageNum) { params["pageNum"] = pageNum; }
            if (searchcondition) { params["searchcondition"] = searchcondition; }
            if (isPaged) { params["isPaged"] = isPaged; }
            if (sortBy) { params["sortBy"] = sortBy; }
            if (sortExp) { params["sortExp"] = sortExp; }
            if (pageSize) { params["pageSize"] = pageSize; }
            return params;
        },
        widgetClick: function (code) {            
            var vueObj = this;
            vueObj.selectedWidgetCode = code.name;
            if (vueObj.selectedWidgetCode == 'In progress') {
                vueObj.selectedWidgetCode = 'inprogress';
            }
            if (vueObj.selectedWidgetCode == 'Completed') {
                vueObj.selectedWidgetCode = 'completed';
            }
            if (vueObj.selectedWidgetCode == 'Need To Reschedule') {
                vueObj.selectedWidgetCode = 'needtoreschedule';
            }
            if (vueObj.selectedWidgetCode == 'Scheduled') {
                vueObj.selectedWidgetCode = 'scheduled';
            }
            if (vueObj.selectedWidgetCode == 'UnScheduled') {
                vueObj.selectedWidgetCode = 'unscheduled';
            }
            vueObj.fetchRecords();
        },
        async fetchRecords() {  
            var vueObj = this;
            vueObj.isLoading=true;
            var srchCondition = "";
            if (!this.isBlank(this.searchCondition)) {
                srchCondition = srchCondition + this.searchCondition;
            }
            if (!this.isBlank(this.conditionForInstantSearch)) {
                srchCondition = srchCondition + ((!this.isBlank(this.searchCondition)) ? (" AND " + this.conditionForInstantSearch) : this.conditionForInstantSearch);
            }
            if (!this.isBlank(this.searchCondition)) { srchCondition = srchCondition + this.searchCondition; }
            if (!this.isBlank(this.conditionForInstantSearch)) {
                srchCondition = srchCondition + ((!this.isBlank(this.searchCondition)) ? (" AND " + this.conditionForInstantSearch) : this.conditionForInstantSearch);
            }
            if (srchCondition != "") {
                if (vueObj.appliedFilter.length > 0) {
                    const removeFilterindex = vueObj.listheaderbuttons.findIndex(object => {
                        return object.callbackfunction === 'removeFilter';
                    });
                    vueObj.listheaderbuttons[removeFilterindex].isvisible = true;
                    const openFilterPopUpindex = vueObj.listheaderbuttons.findIndex(object => {
                        return object.callbackfunction === 'openFilterPopUp';
                    });
                    vueObj.listheaderbuttons[openFilterPopUpindex].iconClass = 'fa fa-filter text-success';
                }
            }
            else {
                if (vueObj.appliedFilter.length == 0) {
                    const removeFilterindex = vueObj.listheaderbuttons.findIndex(object => {
                        return object.callbackfunction === 'removeFilter';
                    });
                    vueObj.listheaderbuttons[removeFilterindex].isvisible = false;
                    const openFilterPopUpindex = vueObj.listheaderbuttons.findIndex(object => {
                        return object.callbackfunction === 'openFilterPopUp';
                    });
                    vueObj.listheaderbuttons[openFilterPopUpindex].iconClass = 'fa fa-filter';
                }
            }
            vueObj.SAData = [];
            var url = `isPaged=${this.isPaged}&pageSize=${vueObj.PageSize}&pageNum=${vueObj.PageNumber}&sortBy=${vueObj.SortBy}&sortExp=${vueObj.SortExp}&code=${vueObj.selectedWidgetCode}`;            
            if (!vueObj.isBlank(vueObj.searchCondition)) {                
                if (vueObj.searchCondition.includes('SA.SA_Number') || vueObj.searchCondition.includes('worder.workorder_number')) {
                    url = url + "&searchCondition=" + encodeURIComponent(this.searchCondition);
                }   
                else {
                    url = url + "&searchCondition=" + vueObj.searchCondition;
                }                         
            } 
            await DataServices.ServiceAppointmentListing(url).then(response => {
                if (response.data != "" && response.data != null) {
                    vueObj.isLoading=false;
                    vueObj.Headers = response.data.schema;
                    vueObj.Headers.forEach((column) => {
                        column.settings = null;
                        if (column.COLUMN_NAME == 'SA_Number') {
                            column.settings = {
                                clickEvent: vueObj.clickEventOfColumn,
                                formatter: vueObj.columnDataFormatter
                            }
                        }
                        if (column.COLUMN_NAME == 'Status') {
                            column.settings = {
                                isInSlot: true
                            }
                        }
                        
                    });
                    if (response.data.data.length > 0) {
                        vueObj.SAData = response.data.data;
                        vueObj.TotalRecords = response.data.data[0].total_records;
                        vueObj.TotalPages = Math.ceil(vueObj.TotalRecords / vueObj.PageSize);                        
                        if (response.data.CompletedSACount == 0) {
                            vueObj.CompletedSACount = 0;
                            vueObj.widgets[0].value=0;
                        }
                        else {
                            vueObj.widgets[0].value = response.data.CompletedSACount;
                        }
                        if (response.data.InProgressSACount == 0) {
                            vueObj.InProgressSACount = 0;
                            vueObj.widgets[1].value=0;
                        }
                        else {
                            vueObj.widgets[1].value = response.data.InProgressSACount;
                        }
                        if (response.data.NeedToRescheduleSACount == 0) {
                            vueObj.NeedToRescheduleSACount = 0;
                            vueObj.widgets[2].value=0;
                        }
                        else {
                            vueObj.widgets[2].value = response.data.NeedToRescheduleSACount;
                        }
                        if (response.data.ScheduledSACount == 0) {
                            vueObj.ScheduledSACount = 0;
                            vueObj.widgets[3].value=0;
                        }
                        else {
                            vueObj.widgets[3].value = response.data.ScheduledSACount;
                        }
                        if (response.data.UnscheduledSACount == 0) {
                            vueObj.UnscheduledSACount = 0;
                            vueObj.widgets[4].value=0;
                        }
                        else {
                            vueObj.widgets[4].value = response.data.UnscheduledSACount;
                        }
                        if (response.data.CancelledSACount == 0) {
                            vueObj.CancelledSACount = 0;
                            vueObj.widgets[5].value=0;
                        }
                        else {
                            vueObj.widgets[5].value = response.data.CancelledSACount;
                        }
                        vueObj.CurrentPage = vueObj.PageNumber;                   
                        vueObj.IsFsmLocating = response.data.IS_FSM_LOCATING;
                        vueObj.listheaderbuttons[5].isdisabled=true;
                        vueObj.noRecord = false;
                    }
                    else {
                        vueObj.noRecord = true;
                        vueObj.SAData = [];
                        vueObj.TotalRecords = 0
                    }
                }
                else {
                    vueObj.noRecord = true;
                    vueObj.SAData = [];
                    vueObj.Headers = [];
                    vueObj.TotalRecords = 0
                }
            });           
            setTimeout(function () { vueObj.ResponsiveDataTable('tablelistingdata') }, 500);
        },
        commonDeleteData: async function (event) {
            var vm = this;
            var val = "";
            if ($('.chkItems:checkbox:checked').prop('checked') == true) {
                $('.chkItems:checkbox:checked').not("[id^='chkAll']").each(function () {
                    if (val.length > 0)
                        val += ',';
                    val += $(this).val();
                });
            } else {
                val += event;
            }
            if (val.length > 0) {
                vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("SetupTicketSource")), function () {
                    var postJSON = {
                        ids: val,
                        moduleName: "Ticketing",
                        subModuleCode: "SERVICE_APPOINTMENT",
                        refCode: "TALYGEN_ServiceAppointment"
                    };
                    var objectPostString = JSON.stringify(postJSON);
                    DataServices.DeleteMultipleOrSingleRecords(objectPostString).then(function (response) {
                        if (response.data[0].Status == 'Success') {                           
                            vm.ShowAlert(vm.$t('DeletedSuccess', [vm.$t('ServiceAppointment')]), "Success", true, vm.$t('Alert'));
                            vm.fetchRecords();
                        }
                        else {
                            vm.ShowAlert(vm.$t('DeletedError', [vm.$t('ServiceAppointment')]), "failure", true, vm.$t('Alert'));
                        }
                    });
                });
            }
            else {
                vm.ShowAlert(vm.$t("Selectanyrecordtodelete"), "warning", true, vm.$t("Alert"));
            }
        },
        openFilter() {
            this.showFilter = true;
        },
        async GetDefaultView() {
            await DataServices.GetDefaultView().then(response => {
                this.IsDefaultView = response.data.result;
            });
        },
        async loadView(view) {
            var vm = this;
            vm.isLoading = true;
            if (view === 'Gantt' && vm.UserType != 'NU') {
                vm.IsListView = false;
            }
            else if (view === 'Gantt' && vm.UserType == 'NU') {
                vm.IsListView = false;
            }
            else if (view === 'List') {
                vm.IsListView = true;
                await vm.fetchRecords()
                await vm.GetDdlRecords_ViewList();
            }
            vm.isLoading = false;
        },
        AddServiceAppointment(ths) {            
            this.showPopupAddServiceAppointment=true;    
            this.SchduleObj=ths;      
        },
        openScheduledAppointmentPopupForEdit: async function (Value) {
            var url = `id=${Value}&isForTickets=${this.ExtraMapFeature}`;
            await DataServices.GetSADetailById(url).then(response => {
                var data = response.data.result;
                this.openScheduledAppointmentPopup(data);
            });
        },
        openScheduledAppointmentPopup: function (e) {
            if (e) {
                if (typeof e.groupId != 'undefined' && e.groupId != null) {
                    this.SA_GroupData = [];
                    this.SA_GroupData.push({ value: parseInt(e.groupId), name: e.groupName, type: e.groupType })
                }
                this.SchduleAppointment = e;
                this.IsSchduleAppointment = true;
            }
        },
        openPopupForSetupTickectSource(ths){         
            this.$router.push({ name: "ViewSADetails", params: { id: ths } });
        }        
    }
}
</script>