<script>
import DataServices from "../../services/DataService";
import $ from "jquery";


export default {
    props: {
        submodulecode: {
            type: String,
            required: true
        },
        modulename: {
            type: String,
            required: true
        },
        callbackfunction: {
            type: Function
        },
        control: {
            type: Object
        },
        viewId: {
            type: Number
        },
        filterType: {
            type: String
        },
        tabId: {
            type: Number
        }
    },
    data: function () {

        return {
            filterData: [],
            showListData: false,
            showAddData: true,
            filterItemData: [],
            fieldNameData: [],
            options: "",
            submited: false,
            databaseCondition: '',
            condition: '',
            filterName: '',
            validationForName: '',
            filterId: 0,
            isDefault: false,
            FormatForSecond: '',
            isFirstTime: false,
            exitstingFilterNameArray: [],
            ApiURL: '',
            CurrentObject: null,
            ViewId: 0,
            FilterType: '',
            TabId: 0,
            popover: {
                visibility: 'click'
            },
            CompanyType: '',
            isEmpty:false
        }
    },
    created: function () {
        this.ViewId = this.viewId;
        this.FilterType = this.filterType;
        this.CurrentObject = this.control;
        this.TabId = this.tabId;
        this.IsExtraMapFeature();
        this.GetSearchFilterItemData();
        this.GetFieldNameData();
        if (this.ViewId > 0) {
            this.EditSearchFilterData(this.ViewId);
        }
        setTimeout(function () {
            $('.modal-dialog').draggable({
                handle: ".modal-header"
            });
        }, 1000);
    },
    methods: {
        IsExtraMapFeature: function () {
            var vueObj = this;
            DataServices.IsExtraMapFeature().then(
                function (response) {
                    vueObj.CompanyType = "";
                    if (response.data != null && response.data != "") {
                        vueObj.CompanyType = response.data;
                    } else {
                        vueObj.CompanyType = 'normal';
                    }
                });
        },
        MakeArray: function (value, type) {
            var array = [];
            var arr = String(value).split(',');
            if (type == "radio" || type == "checkbox") {
                if (arr.length > 0) {
                    for (var item of arr) {

                        if (item.split("|").length > 1) {
                            var person = { name: item.split("|")[0], value: item.split("|")[1] };
                            array.push(person);
                        }
                        else {
                            var person = { name: item, value: item };
                            array.push(person);
                        }
                    }
                }
            }
            else {
                if (arr.length > 0) {
                    for (var item of arr) {
                        var person = { name: item, value: item };
                        array.push(person);
                    }
                }
            }
            return array;
        },
        GetLocalizedValue: function (fieldName, displayName) {
            try {
                if (this.isBlank(displayName)) {
                    var chk = this.$t(String(fieldName).toUpperCase());
                    if (chk != String(fieldName).toUpperCase()) {
                        return chk;
                    }
                    chk = this.$t(String(fieldName));
                    if (chk || /^\s*$/.test(chk)) {
                        return chk;
                    }
                    return fieldName;
                }
                else {
                    var chk = this.$t(String(displayName).toUpperCase());
                    if (chk != String(displayName).toUpperCase()) {
                        return chk;
                    }
                    chk = this.$t(String(displayName));
                    if (chk || /^\s*$/.test(chk)) {
                        return chk;
                    }
                    return displayName;
                }
            }
            catch (ex) { console.log(ex) }
        },
        MakeNormalArray: function (value) {
            if (value) {
                try {
                    return JSON.parse(value);
                }
                catch (ex) {
                    return value;
                }
            }
            else {
                value = [];
            }
        },
        isBlank: function (str) {
            return !str || /^\s*$/.test(str);
        },
        returnDateObject: function (date) {
            var checkDate = null;
            date = date == undefined ? '' : date;

            if (date != '') {
                checkDate = new Date(Date.parse(date));
            }
            return {
                format: 'MM-dd-yyyy',
                useStrict: true,
                useCurrent: false,
                showClear: true,
                showClose: true,
                allowInputToggle: true,
                minDate: checkDate,
                widgetPositioning: {
                    horizontal: 'right',
                    vertical: 'bottom'
                }
            }
        },
        GetValidationF: function (object) {
            var obj = {};
            if (object.is_required) {
                obj['required'] = true;
            }
            if (object.length > 0) {
                obj['max'] = object.length;
            }
            if (object.validation_type == "email") {
                obj['email'] = true;
            }
            if (object.dt_code == "date" || object.dt_code == "datetime") {
                obj['date_format'] = String(Globalize.culture().calendar.patterns.d).toUpperCase();
            }
            if (object.dt_code == "decimal") {
                obj['decimal'] = 2;
            }
            if ((object.name == "mobile_phone" || object.name == "business_phone" || object.name == "fax" || object.name == "zipcode") && this.submodulecode == 'CRM_CONTACTS') {
                obj['numeric'] = true;
                obj['max'] = 15;
            }
            if (object.dt_code == "int" || object.dt_code == "bigint") {
                obj['numeric'] = true;
            }
            if (object.dt_code == "url") {
                obj['url'] = 'require_protocol';
            }
            if (object.regular_expression != null && object.regular_expression != "") {
                obj['regex'] = object.regular_expression;
            }
            return obj;
        },
        onSearch: async function onSearch(search, objItem) {

            var vm = this;
            var selctedValue = objItem.value;
            if (typeof selctedValue == "undefined") {
                selctedValue = 0;
            }
            var param =
                "id=&moduleName=" +
                this.modulename +
                "&type=" +
                objItem.field_code +
                "&search=" +
                escape(search);
            if (objItem.dependent_type == "fill") {
                param += "&refId=" + selctedValue;
            }
            await DataServices.getDDLData(param).then(function (response) {
                var json = [];
                if (response.data == '') {

                }
                if (response.data != '') {
                    json = response.data;
                }
                return objItem.select_options = json.DATA;
            });
        },
        setSelectedValue: function (item, val) {
         //   debugger
            try {
                // item.value = val;
                setTimeout(() => {
                    this.SetCondition();
                    this.validateFilterData();
                }, 100);

            }
            catch (ex) {
            }
        },
        AddSearchData: function () {
           // debugger
            this.GetSearchFilterItemData();
        },
        DeleteSearchItem: function (item, index) {
          //  debugger
            var vm = this;
            var msg = vm.$t("ConfirmDelete");
            vm.confirmR(msg, true, null,
                function () {
                    vm.filterItemData.splice(index, 1);
                    vm.SetCondition();
                });
        },
        GetSearchFilterData: function () {
           // debugger
            var vm = this;
            var param = "moduleName=" + vm.modulename + "&subModuleCode=" + vm.submodulecode;
            this.filterData = [];
            DataServices.SearchCriteriaListing(param)
                .then(function (response) {
                    var json = response.data;
                    if (json != null) {
                        this.filterData = json.DATA;
                        return this.filterData;
                    }

                });
        },
        GetFieldNameData: function () {
            //debugger
            var vm = this;
            var param = "moduleName=" + vm.modulename + "&subModuleCode=" + vm.submodulecode;
            if (this.FilterType == "VIEW") {
                param = param + "&reqFrom=MANAGEVIEW";
            }
            this.fieldNameData = [];
            DataServices.GetFieldName(param).then(function (response) {
                // debugger
                var json = response.data;
                if (json != null) {
                    vm.fieldNameData = json.DATA;
                    if (vm.CompanyType == 'nonlocating') {
                        vm.fieldNameData.forEach((item, index) => {
                            if (item.label == 'parent_sub_status_id' || item.label == 'sub_status_id') {
                                return vm.fieldNameData.splice(index, 1);
                            }
                        })
                    } else {
                        return vm.fieldNameData;
                    }
                }

            });
        },
        GetOperatorData: function ($event, item) {
          //  debugger
            var fieldId = $event.target.value;
            var chkData = $.grep(this.fieldNameData, function (element, index) {
                return element.custom_field_id == fieldId;
            });
            var datatype = '';
            item.dt_code = '';
            item.picklist_options = [];
            item.label = '';
            item.dropdown_type = '';
            item.select_options = '';
            item.select_value = '';
            item.length = 0;
            item.operator = '';
            item.field_code = '';
            item.second_value = '';
            item.display_name = '';
            item.is_required = false;
            if (chkData.length > 0) {
                if (chkData[0].display_name == 'ModifiedBy' || chkData[0].display_name == 'Modified By') {
                    datatype = 'nvarchar';
                }
                else {
                    datatype = chkData[0].sql_type;
                }
                item.dt_code = chkData[0].dt_code;
                item.picklist_options = chkData[0].picklist_options;
                item.label = chkData[0].label;
                item.dropdown_type = chkData[0].dropdown_type;
                item.select_options = chkData[0].select_options;
                item.length = chkData[0].length;
                item.display_name = chkData[0].display_name;
                item.field_code = chkData[0].field_code;
                item.is_required = chkData[0].is_required;
                item.validation_type = chkData[0].validation_type;
            }
            var url = "dataType=" + datatype + "&customField=" + fieldId;
            item.operator_list = [];
            DataServices.GetOperator(url)
                .then(function (response) {
                    var json = response.data;
                    if (json != null) {
                        item.operator_list = json.DATA;
                        return item.operator_list;
                    }

                });
            item.field_name = fieldId;
            item.value = '';
            setTimeout(() => {
                this.SetCondition(); 
                this.validateFilterData();                   
            }, 1000);  
        },
        SetValues: function ($event, item, type) {
            //debugger
            if (item.dt_code == "datetime" || item.dt_code == "decimal" || item.dt_code == "date") {
                if (type == "second") {
                    var value = item.second_value;
                    item.second_value = value;
                }
                else {
                    var value = item.value;
                    item.value = value;
                }
            }
            else {
                if (type == "second") {
                    item.second_value = $event.target.value;
                }
                else {
                    item.value = $event.target.value;
                }
            }
            this.SetCondition();
            this.validateFilterData();
        },
        SetCondition: function () {
           // debugger
            var conditionText = "";
            var databaseCondition = "";
            var vm = this;
            var operatorExpression = "";
            $.each(vm.filterItemData, function (index, item) {
                if (index > 0) {
                    conditionText += " And ";
                    databaseCondition += " and ";
                }
                var fieldId = item.field_name;
                var chkData = $.grep(vm.fieldNameData, function (element) {

                    return element.custom_field_id == fieldId;
                });
                if (typeof (chkData) != 'undefined' && chkData.length > 0) {
                    // debugger
                    conditionText += vm.GetLocalizedValue(chkData[0].label, chkData[0].display_name);
                    if (chkData[0].dt_code == "date" || chkData[0].dt_code == "datetime") {
                        // debugger
                        databaseCondition += "convert(date, " + chkData[0].name + ", 23)";
                    }
                    else {
                        if (chkData[0].name == 'CL.client') {
                            chkData[0].name = 'CL.UserNames';
                        }
                        else if (chkData[0].name == 'PMU.pm') {
                            chkData[0].name = 'PMU.UserNames';
                        } else {
                            databaseCondition += chkData[0].name.trimEnd();
                        }
                    }

                }
                var operatorId = item.operator;
                chkData = $.grep(item.operator_list, function (element, index) {
                    return element.OPERATOR_ID == operatorId;
                });

                if (typeof (chkData) != 'undefined' && chkData.length > 0) {
                    // if(chkData[0].OPERATOR_DISPLAY_NAME=='Is Not Empty' || chkData[0].OPERATOR_DISPLAY_NAME=='Is Empty'){
                    //     databaseCondition += " " + chkData[0].OPERATOR_EXPRESSION;
                    //     conditionText = conditionText + " " + chkData[0].OPERATOR_EXPRESSION;
                    //     operatorExpression = chkData[0].OPERATOR_EXPRESSION
                    // }else{
                    databaseCondition += " " + operatorExpression;
                    conditionText = conditionText + " " + chkData[0].OPERATOR_DISPLAY_NAME;
                    operatorExpression = chkData[0].OPERATOR_EXPRESSION
                    //}

                }
                if (!vm.isBlank(item.value)) {
                 //   debugger
                    var text = item.value;
                    var value = item.value;
                    if (item.dt_code == "select" && item.dropdown_type == "normal" && operatorExpression != "IN" && operatorExpression != "NOT IN") {
                        var array = vm.MakeNormalArray(item.select_options);
                        chkData = $.grep(array, function (element, index) {
                            return element.value == item.value;
                        });
                        if (typeof (chkData) != 'undefined' && chkData.length > 0) {
                            text = chkData[0].name;
                        }

                    }
                    else if ((item.dt_code == "select" && item.dropdown_type == "custom") || (item.dt_code == "select" && item.dropdown_type == "normal" && (operatorExpression == "IN" || operatorExpression == "NOT IN"))) {
                     //   debugger

                        if (Array.isArray(item.value)) {
                            value = vm.createString(item.value, 'value');
                            text = vm.createString(item.value, 'name');
                        }
                        else {
                            value = item.value.value;
                            text = item.value.name;
                        }
                        if (operatorExpression == "IN" || operatorExpression == "NOT IN") {
                            var array = vm.MakeNormalArray(item.select_options);
                            chkData = $.grep(array, function (element, index) {
                                return element.value == item.value;
                            });
                            if (typeof (chkData) != 'undefined' && chkData.length > 0) {
                                text = chkData[0].name;
                            }
                            value = "(" + value + ")";
                        }
                    }
                    else if (item.dt_code == "select" && item.dropdown_type != "custom" && item.dropdown_type != "normal") {
                      //  debugger
                        if ((operatorExpression == "IN" || operatorExpression == "NOT IN")) {
                            if (Array.isArray(item.value)) {
                                value = vm.createString(item.value, 'value');
                                text = vm.createString(item.value, 'name');
                            }
                            else {
                                value = item.value.value;
                                text = item.value.name;
                            }
                            value = "(" + value + ")";
                        } else {
                        //    debugger
                            var array = vm.MakeArray(item.picklist_options, item.dt_code);
                            chkData = $.grep(array, function (element, index) {
                                return element.value == item.value;
                            });
                            if (typeof (chkData) != 'undefined' && chkData.length > 0) {
                                text = chkData[0].name;
                            }
                            value = " '" + value + "'";
                        }

                    }
                    else if (item.dt_code == "date" || item.dt_code == "datetime") {
                        if (operatorExpression == "between") {
                            text = item.value + " And " + item.second_value;
                            value = "'" + vm.$options.filters.formatDatee(item.value) + "'" + " and " + "'" + vm.$options.filters.formatDatee(item.second_value) + "'";

                        }
                        else {
                            text = item.value;
                            value = "'" + vm.$options.filters.formatDatee(item.value) + "'";
                        }
                    }
                    else if (item.dt_code == "radio") {
                        var array = vm.MakeArray(item.picklist_options, item.dt_code);
                        chkData = $.grep(array, function (element, index) {
                            return element.value == item.value;
                        });
                        if (typeof (chkData) != 'undefined' && chkData.length > 0) {
                            text = chkData[0].name;
                            value = "'" + chkData[0].value + "'";
                        }
                    }
                    else {
                     //   debugger
                        if (operatorExpression == "Like" || operatorExpression == "Not Like") {
                            value = "'%" + item.value + "%'";
                        }
                        else if (operatorExpression == "Starts With") {
                            value = "'" + item.value + "%'";
                            databaseCondition = databaseCondition.replace("Starts With", "like");
                        }
                        else if (operatorExpression == "Ends With") {
                            value = "'%" + item.value + "'";
                            databaseCondition = databaseCondition.replace("Ends With", "like");
                        }
                        else if (operatorExpression == "between") {

                            text = item.value + " And " + item.second_value;
                            value = "'" + item.value + "'" + " and " + "'" + item.second_value + "'";
                        }
                        else if (operatorExpression == "IN" || operatorExpression == "NOT IN") {

                            var values = String(item.value).split(",");
                            var str = "";
                            for (var i = 0; i < values.length; i++) {
                                if (i > 0) {
                                    str += " , "
                                }
                                str += "'" + values[i] + "'";
                            }
                            value = "(" + str + ")";
                        }
                        else {
                            value = "'" + value + "'";
                        }
                    }
                 //   debugger
                    if (item.dt_code == "select" && item.dropdown_type == "normal" && item.label == 'event_location') {
                        databaseCondition += " '" + value + "'";
                    }
                    else if (item.dt_code == "select" && item.dropdown_type == "normal" && item.label == 'forecast_category_name') {
                        databaseCondition += " '" + text + "'";
                    }
                    else if (item.dt_code == "select" && item.dropdown_type == "normal" && item.label == 'display_icon') {
                        databaseCondition += " '" + value + "'";
                    }
                    else {
                        databaseCondition += " " + value;
                    }
                    conditionText = conditionText + " " + text;
                }

            });
            vm.condition = conditionText;
            vm.databaseCondition = encodeURIComponent(databaseCondition).replace(/'/g, "%27");

            if (typeof vm.CurrentObject != 'undefined' && vm.CurrentObject != null) {
                vm.CurrentObject.TabCondition = vm.condition;
                vm.CurrentObject.TabDatabaseCondition = vm.databaseCondition;
            }
            else {
                vm.$parent.$parent.ViewData.Condition = vm.condition;
                vm.$parent.$parent.ViewData.DatabaseCondition = vm.databaseCondition;
            }
           // setTimeout(() => {
                vm.SubmitData();                  
            //}, 1000);  
            
        },
        ShowFieldValue: function ($event, item) {
          //  debugger
            var id = $event.target.value;
            var chkData = $.grep(item.operator_list, function (element, index) {
                return element.OPERATOR_ID == id;
            });
            var expression = '';
            if (chkData.length > 0) {
                expression = chkData[0].OPERATOR_EXPRESSION;
            }
            if (expression == 'between') {
                item.showSecondValue = true;
            }
            else {
                item.showSecondValue = false;
            }
            item.operator = id;
            item.operator_expression = expression;
           // debugger
            if(item.operator_expression == 'Is Null' || item.operator_expression == 'Is Not Null'){ 
               this.isEmpty=true;
                item.is_required=false;                
                this.SetCondition();
            }else{
                console.log("UnCheck");
               this.SetCondition();
               this.validateFilterData(); 
            }
            
        },
        GetSearchFilterItemData: function () {            
            var obj = {
                field_name: '',
                dt_code: '',
                operator: '',
                operator_expression: '',
                operator_list: [],
                first_value: '',
                second_value: '',
                showSecondValue: false,
                picklist_options: [],
                label: '',
                display_name: '',
                dropdown_type: '',
                select_options: '',
                length: 0,
                field_code: '',
                select_value: '',
                is_required: false
            };
            this.filterItemData.push(obj);
        },
        ApplyFilter: function (databaseCondition) {
            this.databaseCondition = databaseCondition;
            checkVue.searchCondition = this.databaseCondition;
            if (typeof this.callbackfunction != 'undefined' && this.callbackfunction != null) {
                this.callbackfunction();
            } else {
                this.$parent.PageSize = 10;
                this.$parent.FetchData();
            }
            this.$emit('close');
        },
        EditFilter: function (item, type) {
          //  debugger
            this.showListData = false;
            this.showAddData = true;
            if (type != 'NotSaved') {
                this.filterId = item.FILTER_ID;
                this.EditSearchFilterData(this.filterId);
            } else {
                this.filterName = ((this.$parent.appliedFilter[0].field_name).split('('))[0];
                this.filterItemData = this.$parent.appliedFilter[0];
            }
        },
        deleteFilter: function (item) {
            this.filterId = item.FILTER_ID;
            var vm = this;
            var alertTitle = vm.$t("Delete");
            var msg =
                vm.$t("ConfirmDeleteFilter") +
                vm.$t("Remove");
            vm.confirmR(msg, true, alertTitle, function () {
                var url = "id=" +
                    item.FILTER_ID +
                    "&moduleName=" +
                    vm.modulename +
                    "&subModuleCode=" +
                    vm.submodulecode +
                    "&isDelete=1";

                DataServices.SearchCriteriaById(url)
                    .then(
                        function (response) {
                            var json = response.data;

                            var box = "";
                            if (json != null && json.MSG == "SUCCESS") {
                                vm.GetSearchFilterData();
                                if (item.DATABASE_CONDITION == vm.$parent.searchCondition) {
                                    vm.$parent.searchCondition = "";
                                    if (
                                        typeof vm.callbackfunction != "undefined" &&
                                        vm.callbackfunction != null
                                    ) {
                                        vm.callbackfunction();
                                    } else {
                                        vm.$parent.FetchData();
                                    }
                                }
                                box = vm.ShowAlert(vm.$t("DeletedSuccess", [vm.$t("Filter")]), "success", true, vm.$t("Filter"));
                            }
                        }
                    );
            });
        },
        EditSearchFilterData: function (id) {        
            var vm = this;
            var tabId = 0;
            if (vm.TabId > 0) {
                tabId = vm.TabId;
            }
            var url = "id=" + id + "&type=" + vm.FilterType + "&tabId=" + tabId + "&moduleName=" + vm.modulename + "&subModuleCode=" + vm.submodulecode;
            vm.filterItemData = [];
            DataServices.SearchCriteriaByIdView(url)
                .then(function (response) {                 
                    var json = response.data;
                    if (json != null) {
                        var fullJson = json.DATA;
                        var chkData = $.grep(fullJson, function (element, index) {
                            return element.VIEW_ID == id;
                        });

                        if (chkData != null && chkData.length > 0) {
                            vm.condition = chkData[0].CONDITION;
                            vm.databaseCondition = chkData[0].DATABASE_CONDITION;
                        }
                        if (fullJson.length == 0) {
                            var objab = {
                                field_name: '',
                                dt_code: '',
                                operator: '',
                                operator_expression: '',
                                operator_list: '',
                                value: '',
                                second_value: '',
                                showSecondValue: false,
                                picklist_options: '',
                                label: '',
                                dropdown_type: '',
                                select_options: '',
                                length: '',
                                field_code: '',
                                select_value: '',
                                is_required: false
                            };
                            vm.filterItemData.push(objab);
                        }

                        $.each(fullJson, function (index, item) {
                            var firstValue = item.FIRST_VALUE;
                            var secondValue = item.SECOND_VALUE;
                            if (item.DT_CODE == 'date') {
                                firstValue = vm.formatDate(firstValue);
                                if (item.OPERATOR_EXPRESSION == 'between') {
                                    secondValue = vm.formatDate(secondValue);
                                }
                            }
                            var obj = {
                                field_name: item.FIELD_ID,
                                dt_code: item.DT_CODE,
                                operator: item.OPERATOR_ID,
                                operator_expression: item.OPERATOR_EXPRESSION,
                                operator_list: vm.MakeNormalArray(item.OPERATOR_LIST),
                                value: vm.MakeNormalArray(firstValue),
                                second_value: secondValue,
                                showSecondValue: vm.isBlank(item.SECOND_VALUE) ? false : true,
                                picklist_options: item.PICKLIST_OPTIONS,
                                label: item.LABEL,
                                dropdown_type: item.DROPDOWN_TYPE,
                                select_options: vm.MakeNormalArray(item.SELECT_OPTIONS),
                                length: item.LENGTH,
                                field_code: item.FIELD_CODE,
                                select_value: vm.MakeNormalArray(item.SELECT_OPTIONS),
                                is_required: item.IS_REQUIRED
                            };
                            vm.filterItemData.push(obj);
                        });
                        vm.SubmitData();
                    }

                });

        },
        ClearFilter: function () {
            if (this.$parent.appliedFilter[0].searchCondition == this.$parent.searchCondition) {
                this.$parent.searchCondition = '';
                if (typeof this.callbackfunction != 'undefined' && this.callbackfunction != null) {
                    this.callbackfunction();
                } else {
                    this.$parent.FetchData();
                }
            }
            this.$parent.appliedFilter = [];
        },
        SubmitData: function (type) {
            var vm = this;
            vm.$refs.form.validate().then((result) => {                
                if (vm.isEmpty) {
                    result=true;
                } else {
                    result=false;
                }
                if (result) {
                    vm.$parent.appliedFilter = [];
                    var fullJson = vm.filterItemData;
                    var data = [];
                    $.each(fullJson, function (Key, item) {
                        var value = item.value;
                        var secondValue = item.second_value;
                        if (item.dt_code == "select" && item.dropdown_type == "custom") {
                            if (Array.isArray(item.value)) {
                                value = vm.createString(item.value, 'value');
                            }
                            else {
                                value = item.value.value
                            }
                        }
                        if (item.dt_code == "date") {
                            if (!vm.isBlank(secondValue) && item.operator_expression == "between") {
                                secondValue = vm.$options.filters.formatDate(secondValue)
                            }
                            value = vm.$options.filters.formatDate(item.value)
                        }
                        var obj = {
                            fieldId: item.field_name,
                            operatorId: item.operator,
                            firstValue: value,
                            secondValue: secondValue,
                        };
                        data.push(obj);
                    });

                    if (typeof vm.CurrentObject != 'undefined' && vm.CurrentObject != null) {
                        vm.CurrentObject.TabData = data;
                    }
                    else {
                        vm.$parent.$parent.ViewData.FilterData = data;
                    }

                }
            });
        },
        validateFilterData: function () {            
            var vm = this;
            if (vm.filterItemData.length > 0 && vm.filterItemData[0].field_name != "" && vm.filterItemData[0].field_name != null) {
                //debugger
                if (vm.$parent.$parent.isFilterDataValidationError != undefined) {
                    //debugger
                    vm.$refs.form.validate().then((valid) => {
                        vm.$parent.$parent.isFilterDataValidationError = !valid;
                    });
                }
            } else {
                //debugger
                vm.$parent.$parent.isFilterDataValidationError = false;
            }
        },
        inputEvents() {
          //  debugger
        },
        inputValue() {
          //  debugger
        }
    }
}
</script>
<template>
    <div class="w-100 float-left px-3 listing">
        <div class="row">
            <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 p-0 float-left">
                <div class="form-content border-0">
                    <div id="divManageSearchCriteria" class="col-sm-12 col-md-12 col-lg-12 col-xl-12 p-0 float-left"
                        v-if="showAddData">

                        <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 p-0 float-left">
                            <Form ref="form" v-slot="{ values, meta }">
                                <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 p-0 float-left">
                                    <div class="table-responsive overflow-visible">
                                        <table class="table table-bordered nowrap mt-0 mb-0"
                                            aria-describedby="layoutfilter">
                                            <thead class="thead-bg">
                                                <tr>
                                                    <th scope="col"><span>{{ $t("fieldName") }}</span></th>
                                                    <th scope="col"><span> {{ $t("ConditionalOperator") }}</span></th>
                                                    <th scope="col"><span> {{ $t("Value") }}</span></th>
                                                    <th scope="col" class="text-center"></th>
                                                </tr>
                                            </thead>
                                            <tbody>
                                                <tr v-for="(item, index) in filterItemData" v-bind:key="index">
                                                    <td>
                                                        
                                                        <Field :name="'field_name' + (index + 1)"
                                                            v-bind:data-vv-name="'SearchFilter.fieldName' + (index + 1)"
                                                            :rules="{ 'required': item.is_required }" label="Field Name"
                                                            v-slot="{ errors, changed }">
                                                            <select v-model="item.field_name"
                                                                v-on:change="GetOperatorData($event, item, index)"
                                                                :name="'field_name' + (index + 1)"
                                                                :class="{ 'form-control disableAria': true, 'is-invalid': errors[0] }"
                                                                @update:modelValue="changed">
                                                                <option :data-index="index" value>{{ $t("Select") }}</option>
                                                                <option v-for="(option, optionIndex) in fieldNameData"
                                                                    v-bind:value="String(option.custom_field_id)"
                                                                    :key="optionIndex">
                                                                    {{ $t(option.display_name) }}</option>
                                                            </select>
                                                        </Field>
                                                    </td>
                                                    <td>
                                                       
                                                        <Field :name="'operator' + (index + 1)"
                                                            :rules="{ 'required': item.is_required }" label="operator"
                                                            v-slot="{ errors, changed }">
                                                            <select :name="'operator' + (index + 1)"
                                                                :class="{ 'form-control': true, 'disableAria': true, 'is-invalid': errors[0] }"
                                                                @update:modelValue="changed" v-model="item.operator"
                                                                v-on:change="ShowFieldValue($event, item, index)">
                                                                <option value>{{ $t("Select") }}</option>
                                                                <option v-for="(option, optionIndex1) in item.operator_list"
                                                                    v-bind:value="String(option.OPERATOR_ID)"
                                                                    :key="optionIndex1">
                                                                    {{ option.OPERATOR_DISPLAY_NAME }}</option>
                                                            </select>
                                                        </Field>
                                                    </td>
                                                    <td class="overflow-visible">
                                                        <div v-bind:class="{ 'col-5 p-0 float-left pr-1': item.operator_expression == 'between' }"
                                                            v-if="item.dt_code == 'date' || item.dt_code == 'datetime'">
                                                            <Field :name="'date' + item.label + index"
                                                                :rules="{ 'required': item.is_required }"
                                                                :label="item.label + index" v-slot="{ errors, changed }">
                                                                <v-date-picker :popover="popover" :minute-increment="5"
                                                                    class="input-group" v-model="item.value"
                                                                    v-bind:name="'date' + item.label + index"
                                                                    v-on:dp-hide="item.second_value = ''; SetValues($event, item);"
                                                                    v-bind:class="{ 'disableAria': true, 'is-invalid': (item.dt_code == 'date' || item.dt_code == 'datetime') && errors[0] }"
                                                                    @update:modelValue="changed"
                                                                    @input="SetValues($event, item)">
                                                                    <template
                                                                        v-slot="{ inputValue, inputEvents, togglePopover }">
                                                                        <input class="form-control" :value="inputValue"
                                                                            v-on="inputEvents" />
                                                                        <div @click="togglePopover()"
                                                                            class="input-group-append"><span
                                                                                class="input-group-text"><em
                                                                                    class="fa fa-calendar"></em></span>
                                                                        </div>
                                                                    </template>
                                                                </v-date-picker>
                                                            </Field>
                                                        </div>
                                                        <div style="margin-top: 6px;" class="col-2 float-left"
                                                            v-if="(item.dt_code == 'date' || item.dt_code == 'datetime') && item.operator_expression == 'between'">
                                                            {{ $t('And', 'And') }}</div>
                                                        <div v-bind:class="{ 'input-group': true, 'col-5 p-0 float-left': item.operator_expression == 'between' }"
                                                            v-if="(item.dt_code == 'date' || item.dt_code == 'datetime') && item.operator_expression == 'between'">
                                                            <Field :name="'second' + item.label + index"
                                                                :rules="{ 'required': item.is_required }"
                                                                :label="item.label + index" v-slot="{ errors, changed }">
                                                                <v-date-picker :popover="popover" :minute-increment="5"
                                                                    class="input-group" v-model="item.second_value"
                                                                    v-bind:name="'second' + item.label + index"
                                                                    ref="SecondDatePicker"
                                                                    v-on:dp-hide="item.second_value = ''; SetValues($event, item);"
                                                                    v-bind:class="{ 'disableAria': true, 'is-invalid': (item.dt_code == 'date' || item.dt_code == 'datetime') && errors[0] }"
                                                                    @update:modelValue="changed"
                                                                    @input="SetValues($event, item)">
                                                                    <template
                                                                        v-slot="{ inputValue, inputEvents, togglePopover }">
                                                                        <input class="form-control" :value="inputValue"
                                                                            v-on="inputEvents" />
                                                                        <div @click="togglePopover()"
                                                                            class="input-group-append"><span
                                                                                class="input-group-text"><em
                                                                                    class="fa fa-calendar"></em></span>
                                                                        </div>
                                                                    </template>
                                                                </v-date-picker>
                                                            </Field>
                                                        </div>
                                                        <Field
                                                            v-if="item.picklist_options != 'Lookup' && item.dt_code != 'date' && item.dt_code != 'datetime' && item.dt_code != 'radio'
                                                                && item.dt_code != 'select' && item.dt_code != 'textarea' && item.dt_code != 'checkbox'"
                                                            :name="item.label + index" :rules="{ 'required': item.is_required }"
                                                            label="Value" v-slot="{ errors }">
                                                            <input v-bind:id="item.label + index"
                                                                v-bind:name="item.label + index"
                                                                v-bind:class="{ 'form-control disableAria': true, 'is-invalid': errors[0], 'col-5 float-left': item.operator_expression == 'between' }"
                                                                v-on:input="SetValues($event, item)" type="text"
                                                                v-model="item.value" />
                                                        </Field>
                                                        <div style="margin-top: 6px;" class="col-2 float-left" v-if="item.picklist_options != 'Lookup' && item.dt_code != 'date'
                                                            && item.dt_code != 'datetime' && item.dt_code != 'radio' && item.dt_code != 'select' && item.dt_code != 'textarea' &&
                                                            item.dt_code != 'checkbox' && item.operator_expression == 'between'">
                                                            {{ $t('And', 'And') }}
                                                        </div>
                                                        <Field v-if="item.picklist_options != 'Lookup' && item.dt_code != 'date' &&
                                                            item.dt_code != 'datetime' && item.dt_code != 'radio' && item.dt_code != 'select' && item.dt_code != 'textarea' &&
                                                            item.dt_code != 'checkbox' && item.operator_expression == 'between'"
                                                            :name="'second' + item.label + index"
                                                            :rules="{ 'required': item.is_required }"
                                                            :label="'second' + item.label + index" v-slot="{ errors }">
                                                            <input
                                                                v-bind:class="{ 'form-control disableAria': true, 'is-invalid': errors[0], 'col-5 float-left': item.operator_expression == 'between' }"
                                                                v-on:input="SetValues($event, item, 'second')" type="text"
                                                                v-model="item.second_value" v-bind:id="item.label"
                                                                v-bind:name="'second' + item.label + index" />
                                                        </Field>
                                                        <Field :name="'textarea' + item.label + index"
                                                            v-if="item.dt_code == 'textarea'"
                                                            :rules="{ 'required': item.is_required }" :label="item.label + index"
                                                            v-slot="{ errors }">
                                                            <textarea
                                                                v-bind:class="{ 'form-control': true, 'is-invalid': item.dt_code == 'textarea' && errors[0] }"
                                                                v-model="item.value" v-bind:id="item.label"
                                                                v-on:change="SetValues($event, item)"></textarea>
                                                        </Field>
                                                        <div class="form-control pl-4 border-0 bg-transparent"
                                                            v-if="item.dt_code == 'radio'">
                                                            <div class="form-check form-check-inline">
                                                                <div class="form-check form-check-inline custom-control mx-4 pl-2 custom-radio"
                                                                    v-for="(option, optionIndex2) in MakeArray(item.picklist_options, item.dt_code)"
                                                                    v-bind:key="option.name" :key="optionIndex2">
                                                                    <Field :name="'rdo_' + item.custom_field_id + option.value"
                                                                        :rules="{ 'required': item.is_required }"
                                                                        :label="item.label">
                                                                        <input
                                                                            v-bind:id="'rdo_' + item.custom_field_id + option.value"
                                                                            class="dynamic form-check-input custom-control-input"
                                                                            v-model="item.value"
                                                                            v-bind:name="'rdo_' + item.custom_field_id + option.value"
                                                                            v-on:change="SetValues($event, item)"
                                                                            v-bind:data-resource="option.name"
                                                                            v-bind:value="option.value" type="radio"
                                                                            v-bind:checked="option.value == item.value" />
                                                                    </Field>
                                                                    <label
                                                                        class="custom-control-label universalradio-custom-control-label pl-0 pt-0 dynamic"
                                                                        v-bind:for="'rdo_' + item.custom_field_id + option.value">{{ option.name }}</label>
                                                                </div>
                                                            </div>
                                                        </div>
                                                        <div class="form-control pl-0 border-0 bg-transparent"
                                                            v-if="item.dt_code == 'checkbox'">
                                                            <div class="form-check form-check-inline">
                                                                <div class="custom-control custom-checkbox"
                                                                    v-for="(option, optionIndex3) in MakeArray(item.picklist_options, item.dt_code)"
                                                                    :key="optionIndex3">
                                                                    <Field :name="'chk_' + item.custom_field_id + option.value"
                                                                        :rules="{ 'required': item.is_required }"
                                                                        :label="item.label">
                                                                        <input
                                                                            v-bind:id="'chk_' + item.custom_field_id + option.value"
                                                                            class="dynamic custom-control-input"
                                                                            v-model="item.select_value"
                                                                            v-bind:name="'chk_' + item.custom_field_id + option.value"
                                                                            v-bind:data-resource-id="option.name"
                                                                            v-bind:value="option.value" type="checkbox"
                                                                            v-on:change="SetValues($event, item)" />
                                                                    </Field>
                                                                    <label
                                                                        class="custom-control-label universal-custom-control-label pl-2 pr-1 dynamic"
                                                                        v-bind:for="'chk_' + item.custom_field_id + option.value">{{ option.name }}</label>
                                                                </div>
                                                            </div>
                                                        </div>
                                                        <Field
                                                            v-if="item.picklist_options == 'Lookup' && item.dt_code == 'select' && (item.dropdown_type == 'custom' ||
                                                                (item.dropdown_type == 'normal' && (item.operator_expression == 'IN' || item.operator_expression == 'NOT IN')))"
                                                            :name="'Multiselect' + item.label + index"
                                                            :rules="{ 'required': item.is_required }" :label="item.label + index"
                                                            v-slot="{ errors, changed }">
                                                            <Multiselect
                                                                :multiple="(item.operator_expression == 'IN' || item.operator_expression == 'NOT IN') ? true : false"
                                                                :name="'Multiselect' + item.label + index" v-bind:class="{
                                                                    'disableAria': true, 'is-invalid': item.picklist_options == 'Lookup' &&
                                                                        item.dt_code == 'select' && item.dropdown_type == 'custom' && errors[0]
                                                                }"
                                                                placeholder="Search a value" v-model="item.value"
                                                                :filter-results="false" :searchable="true" track-by="value"
                                                                :show-no-results="false" :internal-search="false"
                                                                :clear-on-select="false" :close-on-select="true"
                                                                :options="(item.dropdown_type == 'normal') ? MakeNormalArray(item.select_options) : item.select_options"
                                                                :object="true" label="name"
                                                                @search-change="onSearch($event, item)"
                                                                @select="setSelectedValue" @update:modelValue="changed">
                                                            </Multiselect>
                                                        </Field>
                                                        <Field
                                                            v-if="item.picklist_options == 'Lookup' && item.dt_code == 'select' && item.dropdown_type == 'normal' &&
                                                                (item.operator_expression != 'IN' && item.operator_expression != 'NOT IN' && item.operator_expression != 'Is Null' && item.operator_expression != 'Is Not Null')"
                                                            :name="'select' + item.label + index" :rules="{ 'required': true }"
                                                            :label="item.operator" v-slot="{ errors, changed }">
                                                            <select :name="'select' + item.label + index"
                                                                v-bind:class="{ 'form-control disableAria': true, 'is-invalid': errors[0] }"
                                                                @update:modelValue="changed"
                                                                v-on:change="SetValues($event, item, index)">
                                                                <option value>{{ $t("Select") }}</option>
                                                                <option
                                                                    v-for="(option, optionIndex4) in MakeNormalArray(item.select_options)"
                                                                    :key="optionIndex4"
                                                                    v-bind:selected="item.value == option.value"
                                                                    v-bind:value="option.value">{{ option.name }}</option>
                                                            </select>
                                                        </Field>

                                                        <!-- <Field
                                                            v-if="item.picklist_options == 'Lookup' && item.dt_code == 'select' && item.dropdown_type == 'normal' &&
                                                                (item.operator_expression == 'Not Null' && item.operator_expression == 'Is Not Null')"
                                                               :name="'select' + item.label + index" :label="item.operator">                                                            
                                                        </Field> -->



                                                        <Field
                                                            v-if="item.picklist_options != 'Lookup' && item.dt_code == 'select' &&
                                                                (item.operator_expression != 'IN' && item.operator_expression != 'NOT IN')"
                                                            :name="'select2' + item.label + index" :rules="{ 'required': true }"
                                                            :label="item.operator" v-slot="{ errors, changed }">
                                                            <select :name="'select2' + item.label + index"
                                                                v-bind:class="{ 'form-control disableAria': true, 'is-invalid': errors[0] }"
                                                                @update:modelValue="changed"
                                                                v-on:change="SetValues($event, item, index)">
                                                                <option value>{{ $t("Select") }}</option>
                                                                <option
                                                                    v-for="(option, optionIndex5) in MakeArray(item.picklist_options, item.dt_code)"
                                                                    :key="optionIndex5" v-if="item.picklist_options != null"
                                                                    v-bind:selected="item.value == option.value"
                                                                    v-bind:value="option.value">{{ option.name }}</option>
                                                            </select>
                                                        </Field>
                                                        <Field
                                                            v-if="item.picklist_options != 'Lookup' && item.picklist_options != null && item.dt_code == 'select' && (item.dropdown_type == 'custom' || (item.dropdown_type == 'normal' &&
                                                                (item.operator_expression == 'IN' || item.operator_expression == 'NOT IN')))"
                                                            :name="'Multiselect2' + item.label + index"
                                                            :rules="{ 'required': item.is_required }" label="name"
                                                            v-slot="{ errors, changed }">
                                                            <Multiselect v-bind:class="{
                                                                'disableAria': true, 'is-invalid': item.picklist_options == 'Lookup' &&
                                                                    item.dt_code == 'select' && item.dropdown_type == 'custom' && errors[0]
                                                            }"
                                                                v-model="item.value" placeholder="Search a value"
                                                                :filter-results="false" :searchable="true" track-by="value"
                                                                :show-no-results="false" :internal-search="false"
                                                                :clear-on-select="false" :close-on-select="true"
                                                                :options="item.select_options" :object="true" label="name"
                                                                @search-change="onSearch($event, item)"
                                                                @select="setSelectedValue(item, $event)"
                                                                @update:modelValue="changed">
                                                            </Multiselect>
                                                        </Field>
                                                    </td>
                                                    <td class="text-center single-action">
                                                        <a href="javascript:void(0);"
                                                            v-on:click="DeleteSearchItem(item, index)">
                                                            <em class="fa fa-trash action-icon text-danger"></em>
                                                        </a>
                                                    </td>
                                                </tr>
                                            </tbody>
                                        </table>

                                        <div class="col-lg-12 text-right py-3">
                                            <a href="javascript:;" v-on:click="AddSearchData" :title="$t('Add')"
                                                class="btn btn-success"><em class="fa fa-plus pr-2"
                                                aria-hidden="true"></em>{{$t("Add")}}</a>
                                    </div>
                                </div>
                            </div>
                            <div class="col-12 float-left p-0 mt-2" v-if="!isBlank(condition)">
                                <div class="alert alert-secondary py-2">
                                    <label class="font-weight-bold mb-0"> {{$t("Condition")}}:</label>
                                    <label class="mb-0">{{condition}}</label>
                                </div>
                            </div>
                            <div class="col-12 float-left p-0 mt-2 d-none">
                                <div class="alert alert-secondary py-2">
                                    <label class="font-weight-bold mb-0">Database Condition:</label>
                                    <label class="mb-0">{{databaseCondition}}</label>
                                </div>
                            </div>
                            <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left py-3 d-none">
                                <div class="form-group">
                                    <div class="search-btm-btn">
                                        <a href="javascript:;" class="btn btn-success formbtn widthhalf"
                                            v-on:click="SubmitData('save')" :title="$t('SaveAndSearch')"><em
                                                class="fa fa-save pr-2"></em>
                                            {{$t("SaveAndSearch")}} </a>
                                        <a href="javascript:;" class="btn btn-success formbtn widthhalf"
                                            v-on:click="SubmitData('search')" :title="$t('Search')"><em
                                                class="fa fa-search pr-2"></em>{{$t("Search")}}</a>
                                        <a href="javascript:;" class="btn btn-danger formbtn widthhalf"
                                            v-on:click="showListData=true;showAddData=false;filterItemData = [];GetSearchFilterItemData();"
                                            :title="$t('Cancel')">
                                            <em class="fa fa-close pr-2"></em>{{$t("Cancel")}}</a>
                                    </div>
                                </div>
                            </div>
                        </Form>
                    </div>
                </div>
            </div>
        </div>
    </div>
</div></template>