<template>
    <div class="tooltip-wrap cstmdsgn">
        <p><strong><em class="fa fa-ticket"></em> {{ $t('WorkOrderNumber') }}:  </strong>  {{RemoveNull(data.workorder_number)}}</p>
        <p><strong><em class="fa fa-ticket"></em>{{ $t('SANumber') }}:  </strong>  {{RemoveNull(data.Subject)}}</p>
        <p><strong><em class="fa fa-ticket"></em> {{ $t('TicketNumber') }}: </strong> {{RemoveNull(data.TicketNumber)}}</p>
        <p><strong><em class="fa fa-cog"></em> {{ $t('WorkType') }}: </strong> {{RemoveNull(data.WorkType)}}</p>
        <p><strong><em class="fa fa-file-o"></em> {{ $t('Subject') }}: </strong> {{RemoveNull(data.SubjectDesc)}}</p>
        <p><strong><em class="fa fa-address-card"></em> {{ $t('AccountName') }}: </strong> {{RemoveNull(data.AccountName)}}</p>
        <p><strong><em class="fa fa-user"></em> {{ $t('Client') }}: </strong> {{RemoveNull(data.ContactName)}}</p>
        <p><strong><em class="fa fa-envelope"></em> {{ $t('Email') }}: </strong> {{data.email}}</p>
        <p><strong><em class="fa fa-user"></em> {{ $t('Status') }}: </strong> {{RemoveNull(data.SA_Status)}}</p>
        <p><strong><em class="fa fa-map-marker"></em> {{ $t('Address') }}: </strong> {{data.Address}}</p>
